import {
  ENVIRONMENT_VARIABLE_SET_VARS,
  ENVIRONMENT_VARIABLE_SET_TIME_MACHINE,
  ENVIRONMENT_VARIABLE_SET_PUSH_TO_QUEUE
} from "../../consts/configurations/configurationsActions";
import advancedConfigReducer from "./advancedConfigReducer";

const environmentVariablesReducer = (state, { payload, type }) => {
  switch (type) {
    case ENVIRONMENT_VARIABLE_SET_VARS:
      return { ...state, variables: { ...state.variables, ...payload } };
    case ENVIRONMENT_VARIABLE_SET_TIME_MACHINE:
      return { ...state, timeMachine: { ...state.timeMachine, ...payload } };
    case ENVIRONMENT_VARIABLE_SET_PUSH_TO_QUEUE:
      return { ...state, pushToQueue: { ...state.pushToQueue, ...payload } };
    default:
      return advancedConfigReducer(state, { payload, type });
  }
};

export default environmentVariablesReducer;
