import React, { useState } from "react";
import PropTypes from "prop-types";

import { Button, Modal, Dropdown } from "@salesforce/design-system-react";

import { DEDUP_SCENARIO_CARD_ACTIONS } from "../../../consts/dedup/componentNames";
import {
  SCENARIO_FETCH_ENUMS_DUPLICATE,
  SCENARIO_FETCH_ENUMS_RUN,
  SCENARIO_FETCH_ENUMS_RESULTS
} from "../../../consts/dedup/scenarioFetchEnums";

const scenarioCardActionPropTypes = {
  isEditMode: PropTypes.bool.isRequired,
  onDelete: PropTypes.func,
  onDuplicate: PropTypes.func
};

const DeleteScenarioPromp = ({ isOpen, onDelete, onClose }) => (
  <Modal
    disableClose
    footer={[
      <Button label="Cancel" onClick={onClose} />,
      <Button
        label="Delete"
        variant="destructive"
        onClick={() => {
          onClose();
          onDelete();
        }}
      />
    ]}
    isOpen={isOpen}
    onRequestClose={onClose}
    prompt="error"
    size="small"
    title={<span>Delete Scenario?</span>}
  >
  </Modal>
);

const ScenarioCardActions = ({ isEditMode, onDelete, onDuplicate, onRun, onViewDuplicates }) => {
  const [isOpen, setIsOpen] = useState(false);
  const deleteButton = (
    <Button
      className="slds-m-horizontal_xx-small"
      iconCategory="utility"
      iconName="delete"
      iconVariant="border-filled"
      onClick={onDelete}
    />
  );
  const utilityButton = (
    <Dropdown
      buttonVariant="icon"
      checkmark
      iconName="settings"
      iconSize="large"
      iconVariant="more"
      align="right"
      onSelect={selected => {
        switch (selected.value) {
          case SCENARIO_FETCH_ENUMS_RUN:
            onRun();
            break;
          case SCENARIO_FETCH_ENUMS_RESULTS:
            onViewDuplicates();
            break;
          default:
            onDuplicate();
        }
      }}
      options={[
        { label: "Run", value: SCENARIO_FETCH_ENUMS_RUN },
        //FIXME: wrong name, imported from wrong location
        { label: "View Result", value: SCENARIO_FETCH_ENUMS_RESULTS },
        { label: "Duplicate", value: SCENARIO_FETCH_ENUMS_DUPLICATE }
      ]}
    />
  );
  const actions = [];

  if (isEditMode === true) {
    actions.push(utilityButton);
    actions.push(deleteButton);
    actions.push(
      <DeleteScenarioPromp isOpen={isOpen} onClose={() => setIsOpen(false)} onDelete={onDelete} />
    );
  }

  return actions;
};

ScenarioCardActions.propType = scenarioCardActionPropTypes;
ScenarioCardActions.displayName = DEDUP_SCENARIO_CARD_ACTIONS;

export default ScenarioCardActions;
