import React, { useCallback } from "react";
import { ExpandableSection, Card } from "@salesforce/design-system-react";

import {
  SCENARIO_SET_NAME,
  SCENARIO_SET_DESCRIPTION,
  SCENARIO_SET_IS_ACTIVE,
  SCENARIO_SET_SCENARIO_ACTION,
  SCENARIO_SET_MATCH_CRITERIA,
  SCENARIO_SET_FILTER_CRITERIA,
  SCENARIO_SET_OBJECT_NAME,
  SCENARIO_SET_FIELDS
} from "../../../consts/dedup/scenarioActions";
import useScenario from "../../../hooks/dedup/useScenario";
import ScenarioCardActions from "./ScenarioCardActions";
import ScenarioGeneral from "./ScenarioGeneral";
import MergeActions from "./MergeActions";
import ScenarioHeader from "./ScenarioHeader";
import ScenarioCriterias from "./ScenarioCriterias";
import { DEDUP_SCENARIO } from "../../../consts/dedup/componentNames";
import { DedupStoreContextProvider } from "../../../contexts/scenarioContext";

const propType = {};

const Scenario = () => {
  const [
    { formState, isEditMode, docPrefixedFields, scenarioId, heading, sObjects },
    { dispatch, deleteScenario, duplicateScenario, runDeduplication, viewDuplicates, onSubmit }
  ] = useScenario();

  const {
    name,
    description,
    is_active,
    object_name,
    fields,
    matchCriteria,
    filterCriteria,
    scenario_action
  } = formState;

  return (
    <DedupStoreContextProvider>
      <form onSubmit={e => onSubmit(e)}>
        <ScenarioHeader heading={heading} />
        <Card
          heading={heading}
          headerActions={
            <ScenarioCardActions
              isEditMode={isEditMode}
              onDelete={() => deleteScenario(scenarioId)}
              onDuplicate={() => duplicateScenario(scenarioId)}
              onRun={() => runDeduplication(scenarioId)}
              onViewDuplicates={() => viewDuplicates(scenarioId)}
            />
          }
        >
          <div className="slds-p-horizontal_medium">
            <ScenarioGeneral
              name={name}
              description={description}
              isActive={is_active}
              onChangeName={useCallback(
                name =>
                  dispatch({
                    type: SCENARIO_SET_NAME,
                    payload: { name }
                  }),
                [name]
              )}
              onChangeDescription={useCallback(
                description =>
                  dispatch({
                    type: SCENARIO_SET_DESCRIPTION,
                    payload: { description }
                  }),
                [description]
              )}
              onChangeIsActive={useCallback(
                isActive =>
                  dispatch({
                    type: SCENARIO_SET_IS_ACTIVE,
                    payload: { is_active: isActive }
                  }),
                [is_active]
              )}
            />
            <ScenarioCriterias
              matchCriteria={matchCriteria}
              filterCriteria={filterCriteria}
              objectName={object_name}
              fields={fields}
              docPrefixedFields={docPrefixedFields}
              sObjects={sObjects}
              onChangeMatchCriteria={rows =>
                dispatch({
                  type: SCENARIO_SET_MATCH_CRITERIA,
                  payload: { matchCriteria: rows }
                })
              }
              onChangeFilterCriteria={rows =>
                dispatch({
                  type: SCENARIO_SET_FILTER_CRITERIA,
                  payload: { filterCriteria: rows }
                })
              }
              onChangeObject={(objectName, objectIndex) =>
                dispatch({
                  type: SCENARIO_SET_OBJECT_NAME,
                  payload: { object_name: objectName, objectIndex: objectIndex }
                })
              }
              onChangeFields={fields =>
                dispatch({
                  type: SCENARIO_SET_FIELDS,
                  payload: { fields }
                })
              }
            />
            <ExpandableSection
              title="Merge Actions"
              nonCollapsible
              className="slds-m-bottom_medium"
            >
              <div className="slds-grid slds-gutters_small">
                <MergeActions
                  scenarioAction={scenario_action}
                  onChange={selection =>
                    dispatch({
                      type: SCENARIO_SET_SCENARIO_ACTION,
                      payload: { scenario_action: selection }
                    })
                  }
                />
              </div>
            </ExpandableSection>
          </div>
        </Card>
      </form>
    </DedupStoreContextProvider>
  );
};

Scenario.propType = propType;
Scenario.displayName = DEDUP_SCENARIO;

export default Scenario;
