import { useReducer, useEffect, useContext } from "react";
import { useParams, useHistory } from "react-router-dom";

import {
  useCloneScenario,
  useCreateScenario,
  useDeleteScenario,
  useUpdateScenario,
  useRunDeduplication,
  useFetchScenario
} from "./useScenarioFetch";
import useFieldsFetch from "../useFieldsFetch";
import { ToastContext } from "../../contexts/toastContext";
import { scenarioReducer, intialState } from "../../reducers/dedup/scenarioReducer";
import {
  SCENARIO_LOAD,
  SCENARIO_SET_RESULTS_TABLE_FIELDS
} from "../../consts/dedup/scenarioActions";
import useObjectsFetch from "../useObjectsFetch";
import { DEDUP_PATH } from "../../consts/addresses";

const useScenario = () => {
  const [state, dispatch] = useReducer(scenarioReducer, intialState);
  const [fetchedScenario, fetchScenario] = useFetchScenario();
  const [isNewScenario, createScenario] = useCreateScenario();
  const [isDeleted, deleteScenario] = useDeleteScenario();
  //TODO: rename useRunDeduplication to useRunScenario
  const [isRunning, runDeduplication] = useRunDeduplication();
  const [isCloned, cloneScenario] = useCloneScenario();
  const [isUpdated, updateScenario] = useUpdateScenario();
  const [docPrefixedFields, fetchFields] = useFieldsFetch();
  const params = useParams();
  const history = useHistory();
  const { openToast } = useContext(ToastContext);
  const scenarioId = Number(params.scenario_id);
  const isEditMode = Number.isInteger(scenarioId);
  const heading = `${isEditMode ? "Update" : "Create"} Scenario`;
  const formState = state.formState;
  const sObjects = useObjectsFetch();

  useEffect(() => {
    if (isEditMode === true) fetchScenario(scenarioId);
  }, [isEditMode]);

  useEffect(() => {
    if (fetchedScenario.data !== null && fetchedScenario.error === null)
      dispatch({
        type: SCENARIO_LOAD,
        payload: {
          isEditMode,
          ...fetchedScenario.data.data
        }
      });
  }, [fetchedScenario.data]);

  //TODO: move objectIndex out from reducer
  useEffect(() => {
    if (state.objectIndex !== "") fetchFields(state.objectIndex);
  }, [state.objectIndex]);

  useEffect(
    () =>
      dispatch({
        type: SCENARIO_SET_RESULTS_TABLE_FIELDS,
        payload: {
          fields: docPrefixedFields
        }
      }),
    [docPrefixedFields]
  );

  useEffect(() => {
    const { isEditMode, formState, objectIndex } = state;
    const { object_name } = formState;
    if (isEditMode === true && object_name !== "" && objectIndex === "" && sObjects.length > 0) {
      //TODO: cleanup hook after first call
      const sObject = sObjects.find(obj => obj.label === object_name);
      if (sObject !== undefined) fetchFields(sObject.id);
    }
  }, [sObjects, state.formState.object_name]);

  //TODO: move scenario success and error messages toast to insolate hook
  useEffect(() => {
    if (isNewScenario.data !== null && isNewScenario.data.data.id) {
      openToast({
        heading: ["New Scenario Created"]
      });
      history.push(`${DEDUP_PATH}/scenarios/${isNewScenario.data.data.id}`);
    } else if (isCloned.data !== null && isCloned.data.data.id) {
      openToast({
        heading: ["Scenario Cloned"]
      });
      history.push(`${DEDUP_PATH}//scenarios/${isCloned.data.data.id}`);
    } else if (isDeleted.status !== null && isDeleted.status === 200) {
      openToast({
        heading: ["Scenario Deleted"]
      });
      history.push(`${DEDUP_PATH}/scenarios`);
    } else if (isRunning.data !== null && isRunning.data.status === "success") {
      openToast({
        heading: ["Scenario is Running"]
      });
    }
  }, [isNewScenario.data, isCloned.data, isDeleted, isUpdated, isRunning]);

  useEffect(() => {
    if (isUpdated.data !== null && isUpdated.error === null)
      openToast({
        heading: "Scenario Updated"
      });
  }, [isUpdated.data]);

  const onSubmit = e => {
    if (state.isEditMode === true) {
      updateScenario(scenarioId, state.editMode);
    } else {
      const { fields, ...createMode } = state.formState;
      createScenario(createMode);
    }
    e.preventDefault();
  };

  const viewDuplicates = scenarioId => history.push(`/grax-app/client/duplicates/${scenarioId}`);

  return [
    {
      formState,
      isEditMode,
      docPrefixedFields,
      scenarioId,
      heading,
      sObjects
    },
    {
      dispatch,
      deleteScenario,
      cloneScenario,
      runDeduplication,
      viewDuplicates,
      onSubmit
    }
  ];
};

export default useScenario;
