import React from "react";

import PropTypes from "prop-types";

import withProtectedExpandableSection from "./withProtectedExpandableSection";
import ProtectedInput from "./ProtectedInput";

const propTypes = {
  queryFile: PropTypes.string.isRequired,
  onBlur: PropTypes.func.isRequired
};

const PointinTimeRestore = ({ queryFile, onBlur }) => {
  return (
    <>
      <div className="slds-grid slds-gutters_small">
        <ProtectedInput
          label="Point-in-Time Restore Query File"
          variableName="queryFile"
          value={queryFile}
          required
          onBlur={onBlur}
        />
      </div>
    </>
  );
};

PointinTimeRestore.propTypes = propTypes;

export default withProtectedExpandableSection("Point in Time Restore")(PointinTimeRestore);
