import React, { useReducer, useEffect } from "react";

import {
  QUICK_START_FORM_INITIAL_LOAD,
  ADVANCED_CONFIG_INITIAL_LOAD,
  QUICK_START_FORM_SET_PROVIDER
} from "../consts/configurations/configurationsActions";
import advancedConfigReducer from "../reducers/configurations/advancedConfigReducer";
import quickStartFormReducer from "../reducers/configurations/quickStartFormReducer";

export const withQuickStartProvider = (
  stateContext,
  dispatchContext,
  initialState = {},
  reducer = quickStartFormReducer
) => ({ children, value }) => {
  const [state, dispatch] = useReducer(reducer, initialState);

  useEffect(() => {
    if (value !== undefined) {
      if (value.provider) {
        dispatch({
          type: QUICK_START_FORM_SET_PROVIDER,
          payload: { provider: value.provider }
        });
      }
      dispatch({
        type: QUICK_START_FORM_INITIAL_LOAD,
        payload: value
      });
    }
  }, [value]);

  return (
    <stateContext.Provider value={state}>
      <dispatchContext.Provider value={dispatch}>{children}</dispatchContext.Provider>
    </stateContext.Provider>
  );
};

// TODO: add component's display name

export const withAdvancedConfigProvider = (
  stateContext,
  dispatchContext,
  initialState = {},
  reducers = advancedConfigReducer
) => ({ children, value }) => {
  const [state, dispatch] = useReducer(reducers, initialState);

  useEffect(() => {
    dispatch({
      type: ADVANCED_CONFIG_INITIAL_LOAD,
      payload: value
    });
  }, [value]);

  return (
    <stateContext.Provider value={state}>
      <dispatchContext.Provider value={dispatch}>{children}</dispatchContext.Provider>
    </stateContext.Provider>
  );
};
