import React from "react";

import { Modal, Button } from "@salesforce/design-system-react";

import TreeModalBranch from "./TreeModalBranch";

const HEADINGS = {
  stopwords: "Stopwords",
  synonyms: "Synonyms"
};
const TreeModal = ({ isOpen, criteria, onChange, onRequestClose }) => {
  return (
    <Modal
      isOpen={isOpen}
      heading={HEADINGS[criteria.variant]}
      onRequestClose={onRequestClose}
      size={"small"}
      footer={[
        <Button label="Cancel" onClick={onRequestClose} />,
        <Button variant="brand" label="Apply" onClick={onRequestClose} />
      ]}
    >
      <TreeModalBranch criteria={criteria} onChange={onChange} />
    </Modal>
  );
};

export default TreeModal;
