import React from "react";
import PropTypes from "prop-types";
import StatefulInput from "../../../../commons/StatefulInput";
import AwsRegions from "../../../../commons/AwsRegions";
import ALL_REGIONS from "../../../../../consts/regions";

const propTypes = {
  url: PropTypes.string,
  accessKey: PropTypes.string,
  secretAccessKey: PropTypes.string,
  region: PropTypes.oneOf([...ALL_REGIONS, ""]),
  onBlur: PropTypes.func.isRequired,
  handleCombobox: PropTypes.func.isRequired
};

const defaultProps = {
  url: "",
  accessKey: "",
  secretAccessKey: "",
  region: ""
};

const AwsElasticsearch = ({ url, accessKey, secretAccessKey, region, onBlur, handleCombobox }) => {
  return (
    <>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput label="Endpoint Name" required value={url} id="url" onBlur={onBlur} />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="Access Key ID"
            required
            secured
            value={accessKey}
            id="accessKey"
            onBlur={onBlur}
          />
        </div>
      </div>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="Secret Access Key"
            required
            secured
            value={secretAccessKey}
            id="secretAccessKey"
            onBlur={onBlur}
          />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <AwsRegions
            value={region}
            label="S3 Region"
            onChange={selections => handleCombobox("region", selections)}
          />
        </div>
      </div>
    </>
  );
};

AwsElasticsearch.propTypes = propTypes;
AwsElasticsearch.defaultProps = defaultProps;

export default AwsElasticsearch;
