import React, { Suspense, lazy } from "react";
import { Tabs, TabsPanel, IconSettings, Spinner } from "@salesforce/design-system-react";
import { MemoryRouter } from "react-router-dom";

import "@salesforce-ux/design-system/assets/styles/salesforce-lightning-design-system.min.css";
import "./App.css";

import { isEmbeded, isAppleDevice } from "./lib/utils";
import routes from "./consts/routes";
import NestedRoutes, { initialEntries } from "./components/commons/NestedRoutes";
import { ASSETS_LIGHTNING_DESIGN_ICONS } from "./consts/addresses";

const GRAXRestoreTabs = lazy(() => import("./components/GRAXRestoreTabs"));
const GRAXLogging = lazy(() => import("./components/GRAXLogging"));
const GRAXHistory = lazy(() => import("./components/GRAXHistory"));
const GRAXDelete = lazy(() => import("./components/GRAXDelete"));
const GRAXAudit = lazy(() => import("./components/GRAXAudit"));
const GRAXSummaryCharts = lazy(() => import("./components/GRAXSummaryCharts"));

let appName;

if (window.__grax__ != null && window.__grax__.appName != null) {
  appName = window.__grax__.appname;
} else {
  appName = window.location.pathname.split("/").pop() || "cloud";
}

if (process && process.env && !window.__grax__) {
  window.__grax__ = {
    orgid: process.env.REACT_APP_SFDCORGID,
    pageParams: { authorization: process.env.REACT_APP_BASIC_TOKEN }
  };
}

const helpLinkbyAppName = {
  // 'mapping': 'grax-restore',
  summarycharts: "summary-charts",
  delete: "datalake-delete",
  timemachine: "grax-object-time-machine",
  logging: "grax-logs"
};

class GRAXApp extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoading: true
    };
  }

  componentDidMount() {
    if (appName === "summarycharts") {
      const script = document.createElement("script");
      script.src = "https://www.gstatic.com/charts/loader.js";
      script.async = true;
      script.onload = () => {
        this.setState({ isLoading: false });
      };

      document.body.appendChild(script);
    } else {
      this.setState({ isLoading: false });
    }
  }

  render() {
    if (this.state.isLoading) {
      return <Spinner size="large" variant="brand" />;
    }

    return (
      <React.Fragment>
        {isAppleDevice && (
          <style>
            {`
                    html, body {
                        height: 100%;
                        overflow: auto;
                        -webkit-overflow-scrolling: touch;
                    }
                `}
          </style>
        )}
        <div className={isEmbeded ? "slds-tabs_scoped__content" : ""}>
          {helpLinkbyAppName[appName] != null ? (
            <div
              aria-hidden="false"
              id="GRAXHelpLink"
              style={{
                position: "absolute",
                right: "1.5rem",
                top: "0.75rem"
              }}
            >
              <h3>
                <a
                  href={`https://documentation.grax.io/docs/${helpLinkbyAppName[appName]}`}
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  GRAX Help
                </a>
              </h3>
            </div>
          ) : null}
          <IconSettings iconPath={ASSETS_LIGHTNING_DESIGN_ICONS}>
            <Suspense fallback={<Spinner size="large" variant="brand" />}>
              {(appName === "restore" || appName === "mapping") && (
                <GRAXRestoreTabs variant={isEmbeded ? "default" : "scoped"} appName={appName} />
              )}
              {appName === "logging" && (
                <Tabs variant={isEmbeded ? "default" : "scoped"} id="grax-logging-tabs">
                  <TabsPanel label="Error Logs">
                    <GRAXLogging
                      columns={["CreatedDate", "source", "info", "error", "stack", "description"]}
                    />
                  </TabsPanel>
                  <TabsPanel label="Restore Logs">
                    <GRAXLogging
                      subtype="restore"
                      columns={[
                        "CreatedDate",
                        "objecttype",
                        "sourceId",
                        "destinationId",
                        "status",
                        "error",
                        "description"
                      ]}
                    />
                  </TabsPanel>
                </Tabs>
              )}
              {appName === "timemachine" && (
                <Tabs variant={isEmbeded ? "default" : "scoped"} id="grax-history-tabs">
                  <TabsPanel label="History">
                    <GRAXHistory />
                  </TabsPanel>
                </Tabs>
              )}
              {appName === "delete" && (
                <Tabs variant={isEmbeded ? "default" : "scoped"} id="grax-delete-tabs">
                  <TabsPanel label="Delete">
                    <GRAXDelete />
                  </TabsPanel>
                </Tabs>
              )}
              {appName === "audit" && (
                <GRAXAudit columns={["LastModifiedDate", "ObjectType", "ObjectVersion"]} />
              )}
              {appName === "summarycharts" && (
                <GRAXSummaryCharts variant={isEmbeded ? "default" : "scoped"} />
              )}
              <MemoryRouter initialEntries={initialEntries(appName)}>
                <NestedRoutes routes={routes} />
              </MemoryRouter>
            </Suspense>
          </IconSettings>
        </div>
      </React.Fragment>
    );
  }
}

export default GRAXApp;
