import React from "react";

import { DataTableColumn, DataTableCell, Dropdown } from "@salesforce/design-system-react";

import {
  SCENARIO_FETCH_ENUMS_RUN,
  SCENARIO_FETCH_ENUMS_DUPLICATE,
  SCENARIO_FETCH_ENUMS_UPDATE,
  SCENARIO_FETCH_ENUMS_DELETE
} from "../../../consts/dedup/scenarioFetchEnums";

const ScenariosRowActions = ({ children, onRowAction, ...props }) => {
  const options = [];
  if (props.item.job_status !== "SUCCESS") {
    options.push({
      value: SCENARIO_FETCH_ENUMS_RUN,
      label: "Run"
    });
  }

  options.push(
    { value: SCENARIO_FETCH_ENUMS_DUPLICATE, label: "Clone" },
    { value: SCENARIO_FETCH_ENUMS_UPDATE, label: "Edit" },
    { value: SCENARIO_FETCH_ENUMS_DELETE, label: "Delete" }
  );

  return (
    <DataTableCell {...props}>
      <Dropdown
        align={"right"}
        iconCategory="utility"
        iconName="down"
        iconVariant="border-filled"
        onSelect={selected => onRowAction(props.item.id, selected)}
        buttonClassName="slds-button_icon-small"
        options={options}
      />
    </DataTableCell>
  );
};

const ScenariosViewDuplicates = ({ children, ...props }) => (
  <DataTableCell {...props}>
    {props.item.job_status === "SUCCESS" ? (
      <a href={e => e.preventDefault()} onClick={() => props.viewDuplicates(props.item.id)}>
        View Duplicates
      </a>
    ) : (
      "View Duplicates"
    )}
  </DataTableCell>
);

const DuplicateCounts = ({ children, ...props }) => {
  let duplicateCounts = "";
  if (props.item.groups_count !== null && props.item.records_affected !== null)
    duplicateCounts = `${props.item.groups_count} of ${props.item.records_affected}`;
  return <DataTableCell {...props}>{duplicateCounts}</DataTableCell>;
};

const ScenarioRowLink = ({ children, ...props }) => (
  <DataTableCell {...props}>
    <a href={e => e.preventDefault()} onClick={() => props.viewScenario(props.item.id)}>
      {children}
    </a>
  </DataTableCell>
);

const ScenarioRowFields = ({ children, ...props }) => {
  let fields = props.item.fields;
  const numberOfFields = fields.length;
  let fieldNames = "";
  const fieldLimit = 2;

  if (numberOfFields > 0) {
    fieldNames = fields
      .slice(0, fieldLimit)
      .map(docField => docField.split(".")[1])
      .join(", ");

    if (numberOfFields > 2) {
      fieldNames += ` +${numberOfFields - fieldLimit}`;
    }
  }

  return <DataTableCell>{fieldNames}</DataTableCell>;
};

const IsActiveScenario = ({ children, ...props }) => (
  <DataTableCell {...props}>{!!children ? "Yes" : "No"}</DataTableCell>
);

ScenarioRowLink.displayName = DataTableCell.displayName;
DuplicateCounts.displayName = DataTableCell.displayName;
ScenariosRowActions.displayName = DataTableCell.displayName;
ScenariosViewDuplicates.displayName = DataTableCell.displayName;
ScenarioRowFields.displayName = DataTableCell.displayName;
IsActiveScenario.displayName = DataTableCell.displayName;

const ScenariosColumns = [
  <DataTableColumn key="scenario-name" property="name" label="Name">
    <ScenarioRowLink />
  </DataTableColumn>,
  <DataTableColumn key="object-name" property="object_name" label="Object Name" />,
  <DataTableColumn key="fields" property="fields" label="Fields">
    <ScenarioRowFields />
  </DataTableColumn>,
  <DataTableColumn key="merge-actions" property="scenario_action" label="Merge Action" />,
  <DataTableColumn key="fields" property="is_active" label="Is Active">
    <IsActiveScenario />
  </DataTableColumn>,
  <DataTableColumn key="duplicate-counts" label="Duplicate Counts">
    <DuplicateCounts />
  </DataTableColumn>,
  <DataTableColumn key="view-results" property="job_status">
    <ScenariosViewDuplicates />
  </DataTableColumn>,
  <DataTableColumn key="row-actions" property="job_status" label="">
    <ScenariosRowActions />
  </DataTableColumn>
];

export { ScenariosColumns };
