import React, { cloneElement } from 'react'
import PropTypes from 'prop-types'

import { AddRowButton } from "./Buttons"

const propTypes = {
    rows: PropTypes.arrayOf(PropTypes.object).isRequired,
    onChange: PropTypes.func.isRequired,
    header: PropTypes.element,
    footer: PropTypes.element
}

const withRepeating = WrappedComponent => ({
    rows,
    onChange,
    footer,
    hideFooter,
    ...props
}) => {
    const addRow = (row = {}) => onChange(rows.concat(row));
    const removeRow = index => onChange(rows.filter((r, i) => index !== i));
    const editRow = (row, index) => onChange(rows.map((r, i) => index === i ? row : r));
    const displayName = `${withRepeating.name}${WrappedComponent.name}`;
    const repeatedWrapped = [];

    WrappedComponent.displayName = displayName;
    rows.map((row, index) =>
        repeatedWrapped.push(<WrappedComponent
            row={row}
            index={index}
            key={`${displayName}-${index}`}
            onChange={row => editRow(row, index)}
            onRemove={() => removeRow(index)}
            {...props}
        />)
    );

    const renderFooter = (footer, hideFooter) => {
        if (hideFooter === undefined) {
            if (React.isValidElement(footer)) {
                return cloneElement(footer, { addRow });
            }

            return <AddRowButton onClick={() => addRow({})} />;
        }
    }

    return [
        repeatedWrapped,
        renderFooter(footer, hideFooter)
    ];
}

withRepeating.displayName = 'withRepeating';
withRepeating.propTypes = propTypes;

export default withRepeating