import { useState, useEffect } from "react";

import { GRAX_DATA_LAKE_PROXY_ADDRESS } from "../consts/addresses";
import useFetch from "./useFetch";

const useFieldsFetch = () => {
  const [docPrefixedFields, setdocPrefixedFields] = useState([]);
  const [fields, setObjectIndex] = useFetch();
  const fetchFields = objectIndex => {
    if (objectIndex !== "") {
      setObjectIndex({
        url: `${GRAX_DATA_LAKE_PROXY_ADDRESS}/getDataTypeFields/${objectIndex}?action=load`,
        params: { sfdcorgid: "" }
      });
    } else {
      setdocPrefixedFields([]);
    }
  };

  useEffect(() => {
    // verify network request status
    if (fields.data !== null) {
      const options = [];
      const objectFields = fields.data;
      for (let fieldName in objectFields) {
        if (fieldName.split(".").length === 1 && fieldName.startsWith("grax") === false) {
          const fieldDataType = objectFields[fieldName];
          options.push({
            label: fieldName,
            //TODO: does it need check for doc. prefixed field name?
            value: `doc.${fieldName}`,
            dataType: fieldDataType
          });
        }
      }
      setdocPrefixedFields(options);
    }
  }, [fields.data]);

  return [docPrefixedFields, fetchFields];
};

export default useFieldsFetch;
