import { BASIC_CONFIG_SET_VALUE } from "../../../../consts/configurations/configurationsActions";

import { useHandleInput } from "../../../../hooks/useHandleForm";

export const useSFDCHandleInput = (variant, dispatch, actionType = BASIC_CONFIG_SET_VALUE) => {
  const handleInput = useHandleInput(actionType, dispatch);

  return (event) => handleInput(event, { meta: { variant } });
};

export default useSFDCHandleInput;
