import { useState, useEffect } from "react";

import { useScenarioStore } from "../../contexts/scenarioContext";

export const useOperators = () => {
  const [dataType, setDataType] = useState(null);
  const [fieldOperators, setFieldOperators] = useState([]);
  const scenarioStore = useScenarioStore();
  const { operators } = scenarioStore.metadata.data.data;

  useEffect(() => {
    if (dataType === null) setFieldOperators([]);
    else {
      const applicableOperators = operators
        .filter(operator => operator.applicable_data_types.includes(dataType))
        //TODO: map operators just one
        .map(operator => ({
          label: operator.name,
          value: operator.id
        }));
      setFieldOperators(applicableOperators);
    }
  }, [dataType, operators]);

  return [fieldOperators, setDataType];
};
