import React from "react";
import {
  ELASTICSEARCH_PROVIDERS_BONSAI,
  ELASTICSEARCH_PROVIDERS_AWS,
  ELASTICSEARCH_PROVIDERS_ELASTIC_CO
} from "../../../../../consts/configurations/elasticsearchProviders";
import { BASIC_CONFIG_SET_VALUE } from "../../../../../consts/configurations/configurationsActions";
import { useHandleInput, useHandleCombobox } from "../../../../../hooks/useHandleForm";

import BonsaiElasticsearch from "./BonsaiElasticsearch";
import AwsElasticsearch from "./AwsElasticsearch";
import ElasticCo from "./ElasticCo";

const ProviderFactory = props => {
  const onBlur = useHandleInput(BASIC_CONFIG_SET_VALUE, props.dispatch);
  const handleCombobox = useHandleCombobox(BASIC_CONFIG_SET_VALUE, props.dispatch);

  switch (props.selectedTab) {
    case ELASTICSEARCH_PROVIDERS_BONSAI:
      return (
        <BonsaiElasticsearch url={props.url} gatewayPath={props.gatewayPath} onBlur={onBlur} />
      );
    case ELASTICSEARCH_PROVIDERS_AWS:
      return (
        <AwsElasticsearch
          url={props.url}
          accessKey={props.accessKey}
          secretAccessKey={props.secretAccessKey}
          region={props.region}
          bucket={props.bucket}
          onBlur={onBlur}
          handleCombobox={handleCombobox}
        />
      );
    case ELASTICSEARCH_PROVIDERS_ELASTIC_CO:
      return (
        <ElasticCo
          user={props.user}
          password={props.password}
          url={props.url}
          port={props.port}
          onBlur={onBlur}
        />
      );
    default:
      return null;
  }
};

export default ProviderFactory;
