import {
  SALESFORCE_LOGIN_SFDC,
  SALESFORCE_DATASOURCES_SOURCE,
  SALESFORCE_DATASOURCES_EINSTEIN,
  SALESFORCE_DATASOURCES_BULKRESTORE,
  SALESFORCE_LOGIN_OAUTH,
} from "./consts";

import { isSalesforceCredential } from "./utils";

import basicConfigurationReducer from "../../../../reducers/configurations/quickStartFormReducer";
import { QUICK_START_FORM_INITIAL_LOAD } from "../../../../consts/configurations/configurationsActions";
import { findDataSource } from "../utils";

const salesforceConnectionReducer = (state, { type, payload, meta }) => {
  switch (type) {
    case QUICK_START_FORM_INITIAL_LOAD: {
      const { dataSources, globals } = payload;

      return Object.entries({
        [SALESFORCE_DATASOURCES_SOURCE]: globals.asyncAttachmentProcessorOrganizationIds,
        [SALESFORCE_DATASOURCES_EINSTEIN]: globals.syncSalesforceEinsteinOrganizationIds,
        [SALESFORCE_DATASOURCES_BULKRESTORE]: null,
      }).reduce((result, dataSourceMapping) => {
        const newState = result;
        const [dataSourceType, organizationid] = dataSourceMapping;
        const credential = findDataSource(dataSources, organizationid, dataSourceType);
        const provider = isSalesforceCredential(credential)
          ? SALESFORCE_LOGIN_SFDC
          : SALESFORCE_LOGIN_OAUTH;

        let name;
        if (credential && credential.name) {
          ({ name } = credential);
        } else {
          ({ name } = state[dataSourceType]);
        }

        newState[dataSourceType] = {
          ...state[dataSourceType],
          [provider]: { ...credential, type: dataSourceType },
          provider,
          name,
        };

        return newState;
      }, {});
    }
    default:
      return {
        ...state,
        [meta.variant]: {
          ...basicConfigurationReducer(state[meta.variant], {
            type,
            payload: { ...payload, type: meta.variant },
          }),
        },
      };
  }
};

export default salesforceConnectionReducer;
