import _ from "lodash";

import { CONFIGURATION_DEFAULTENVIRONMENTNAME } from "../consts";
import {
  SALESFORCE_DATASOURCES_SOURCE,
  SALESFORCE_DATASOURCES_EINSTEIN,
  SALESFORCE_DATASOURCES_BULKRESTORE,
} from "../salesforceConnection/consts";

export const omitEmptyProps = (obj) => {
  if (_.isPlainObject(obj) === true) {
    return Object.entries(obj).reduce((accumulator, [key, value]) => {
      const omittedEmptyProps = { ...accumulator };
      if (value !== "" && value !== undefined && value !== null) {
        omittedEmptyProps[key] = obj[key];
      }
      return omittedEmptyProps;
    }, {});
  }

  return obj;
};

export const objHasCollectionOfProps = (obj, properties) => {
  if (!_.isPlainObject(obj) || !_.isArray(properties)) {
    return false;
  }

  for (const property in properties) {
    if (!Object.prototype.hasOwnProperty.call(obj, properties[property])) {
      return false;
    }
  }

  return true;
};

export const selectCurrentTab = (state) => {
  if (state.provider === undefined) throw new Error("No Provider Tab Selected");
  const { provider } = state;
  return state[provider];
};

export const isAnExistingConfiguration = (configuration) => {
  if (
    _.isPlainObject(configuration) === true &&
    _.isPlainObject(configuration.environments) === true &&
    Object.keys(configuration.environments).length > 0
  ) {
    return true;
  }

  return false;
};

export const getFirstEnvironment = (configuration) => {
  if (isAnExistingConfiguration(configuration) === true) {
    return configuration.environments[getFirstEnvironmentName(configuration)];
  }

  return null;
};

export const getFirstEnvironmentName = (configuration) => {
  let environmentNames = [];
  if (isAnExistingConfiguration(configuration)) {
    environmentNames = Object.keys(configuration.environments);
  }
  return environmentNames[0] || CONFIGURATION_DEFAULTENVIRONMENTNAME;
};

export const mergeConfigurations = (configuration, newEnvironment) => ({
  ...(configuration || { globalEnvironmentDefaults: {} }),
  environments: {
    [getFirstEnvironmentName(configuration)]: {
      ...getFirstEnvironment(configuration),
      ...newEnvironment,
    },
  },
});

export const findDataSourcesByOrganizationgIds = (dataSources, organizationIds, types) => {
  let filteredDataSources = [];
  if (Array.isArray(dataSources)) {
    filteredDataSources = dataSources.filter((dataSource) => {
      if (dataSource) {
        if (dataSource.type && Array.isArray(types)) {
          return types.indexOf(dataSource.type) !== -1;
        }

        return (
          Array.isArray(organizationIds) &&
          organizationIds.indexOf(dataSource.organizationId) !== -1
        );
      }

      return false;
    });
  }

  return filteredDataSources;
};

export const findDataSource = (dataSources, organizationId, type) => {
  const organizationIds = organizationId ? [organizationId] : organizationId;
  const types = type ? [type] : type;
  const foundDataSource = findDataSourcesByOrganizationgIds(dataSources, organizationIds, types)[0];

  if (foundDataSource !== null && type) {
    return {
      ...foundDataSource,
      type,
    };
  }

  return foundDataSource || null;
};

export const findSourceDataSource = (dataSources, organizationId) =>
  findDataSource(dataSources, organizationId, SALESFORCE_DATASOURCES_SOURCE);

export const findEinsteinDestination = (dataSources, organizationId) =>
  findDataSource(dataSources, organizationId, SALESFORCE_DATASOURCES_EINSTEIN);

export const findBulkRestoreDestination = (dataSources, organizationId) =>
  findDataSource(dataSources, organizationId, SALESFORCE_DATASOURCES_BULKRESTORE);
