import React from "react";
import PropTypes from "prop-types";

import { SALESFORCE_LOGIN_PRODUCTIONURL, SALESFORCE_LOGIN_SANDBOXURL } from "./consts";

import StatefulInput from "../../../commons/StatefulInput";

const propTypes = {
  // eslint-disable-next-line react/require-default-props
  url: PropTypes.string,
  onBlur: PropTypes.func.isRequired,
  // eslint-disable-next-line react/require-default-props
  inputRef: PropTypes.oneOfType([PropTypes.func, PropTypes.shape({ current: Element })]),
};

export const loginURLHelpText = `${SALESFORCE_LOGIN_PRODUCTIONURL} for Production | ${SALESFORCE_LOGIN_SANDBOXURL} for Sandbox`;

const URLStateFulInput = ({ url, onBlur, inputRef }) => {
  return (
    <StatefulInput
      label="SFDC URL"
      helpText={loginURLHelpText}
      inputRef={inputRef}
      type="url"
      required
      value={url}
      id="url"
      onBlur={onBlur}
      onInvalid={(e) => {
        e.target.setCustomValidity(`Invalid URL. ${loginURLHelpText}`);
      }}
      onInput={(e) => {
        e.target.setCustomValidity("");
      }}
    />
  );
};

URLStateFulInput.propTypes = propTypes;

export default URLStateFulInput;
