import React, { useState, useEffect } from "react";

import TreeModalBranchItem from "./TreeModalBranchItem";
import { useScenarioStore } from "../../../../contexts/scenarioContext";

const TreeModalBranch = ({ criteria, onChange, onSelect }) => {
  const [rows, setRows] = useState([]);
  const scenarioStore = useScenarioStore();
  const { stopwords, synonyms } = scenarioStore.metadata.data.data;

  useEffect(() => {
    if (criteria.variant === "stopwords") setRows(stopwords);
    else setRows(synonyms);
  }, [criteria]);

  return (
    <table className="slds-table slds-table_bordered slds-no-row-hover slds-table_edit">
      <thead>
        <tr className="slds-line-height_reset">
          <th className="" style={{ width: "1rem" }} />
          <th>
            <a
              className="slds-th__action slds-text-link_reset"
              href="javascript:void(0);"
              role="button"
              tabindex="0"
            >
              <span className="slds-assistive-text">Sort by: </span>
              <div className="slds-grid slds-grid_vertical-align-center slds-has-flexi-truncate">
                <span className="slds-truncate" title="Account Name">
                  Name
                </span>
              </div>
            </a>
          </th>
          <th aria-label="Close Date" aria-sort="none" className="" scope="col" />
        </tr>
      </thead>
      <tbody>
        <TreeModalBranchItem rows={rows} selectedId={criteria.id} hideFooter={true} />
      </tbody>
    </table>
  );
};

export default TreeModalBranch;
