import React, { useState } from "react";
import { Button, Radio } from "@salesforce/design-system-react";

import withRepeating from "../../../commons/withRepeating";

const TreeModalRow = ({ row, selectedId, onChange, onRemove }) => {
  const [isEditMode, setIsEditMode] = useState(false);
  return (
    <tr className="slds-hint-parent">
      <th className="slds-cell-edit">
        <Radio variant="base" checked={selectedId === row.id} />
      </th>
      <th className="slds-cell-edit">
        <span className="slds-grid slds-grid_align-spread">
          <a className="slds-truncate">
            <span className="slds-grid slds-grid_align-spread">
              <span className="slds-truncate" title="Acme">
                {row.name}
              </span>
            </span>
          </a>

          <Button
            iconSize="small"
            onClick={() => setIsEditMode(true)}
            iconCategory="utility"
            variant="icon"
            iconName="edit"
            iconVariant="bare"
            className="slds-cell-edit__button"
            iconClassName="slds-button__icon_hint slds-button__icon_edit"
          />
        </span>
        {isEditMode && (
          <section
            className="slds-popover slds-popover slds-popover_edit"
            role="dialog"
            style={{ position: "absolute", top: "0px", left: "0.0625rem" }}
          >
            <div className="slds-popover__body" id="dialog-body-id-1">
              <div className="slds-form-element slds-grid slds-wrap">
                <label
                  className="slds-form-element__label slds-form-element__label_edit slds-no-flex"
                  for="company-01"
                >
                  <span className="slds-assistive-text">Company</span>
                </label>
                <div className="slds-form-element__control slds-grow">
                  <input
                    className="slds-input"
                    id="company-01"
                    type="text"
                    value="Acme Enterprises"
                    onBlur={() => {
                      setIsEditMode(false);
                    }}
                  />
                </div>
              </div>
            </div>
          </section>
        )}
      </th>
      <td className="slds-cell-edit slds-text-align_right">
        <Button
          iconSize="small"
          onClick={onRemove}
          iconCategory="utility"
          variant="icon"
          iconName="delete"
          iconVariant="border-filled"
        />
      </td>
    </tr>
  );
};

export default withRepeating(TreeModalRow);
