import { useEffect, useState, useContext } from "react";
import { useParams } from "react-router-dom";
import { useFetchDuplicates } from "./useScenarioFetch";
import { ToastContext } from "../../contexts/toastContext";

const useDuplicates = () => {
  const [rows, setRows] = useState([]);
  const [tableFields, setTableFields] = useState([]);
  const [tableFieldsExpanded, setTableFieldsExpanded] = useState([]);
  const [indexName, setIndexName] = useState(null);
  const [duplicates, fetchDuplicates] = useFetchDuplicates();
  const params = useParams();
  const { openToast } = useContext(ToastContext);
  const scenarioId = params.scenario_id;

  useEffect(() => fetchDuplicates(scenarioId), [scenarioId]);
  useEffect(() => {
    const data = duplicates.data.data;
    const results = data.results;
    //TODO: move scenario job status to a const file
    if (results.length > 0) {
      // const fields = data.results_table_fields.map(field => field.split(".")[1]);
      const fields = [
        "doc.Name",
        "doc.Website",
        "doc.Phone",
        "doc.BillingCity",
        "doc.BillingState"
      ].map(field => field.split(".")[1]);

      setRows(results);
      setIndexName(results[0].index_name);
      setTableFields(fields.slice(0, 4));
      setTableFieldsExpanded(fields);
    }
  }, [duplicates.data]);

  useEffect(() => {
    const jobStatus = duplicates.data.data.job_status;

    if (jobStatus === "CREATED") {
      openToast({ heading: ["Scenario Job Created"] });
    }
  }, [duplicates.data.data.job_status]);

  return { tableFields, tableFieldsExpanded, rows, indexName };
};

export default useDuplicates;
