import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";

import { Input } from "@salesforce/design-system-react";

import { DEDUP_SCENARIO_FILTER_CRITERIAS } from "../../../consts/dedup/componentNames";
import { RepeatingRemoveRowButton } from "../../commons/Buttons";
import { mutateRow } from "../../../lib/utils";
import withRepeating from "../../commons/withRepeating";
import FieldsAndOperators from "./FieldsAndOperators";

const propTypes = {
  row: PropTypes.shape({
    field: PropTypes.string,
    operator: PropTypes.string,
    value: PropTypes.any
  }),
  index: PropTypes.number,
  onChange: PropTypes.func,
  onRemove: PropTypes.func,
  docPrefixedFields: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string
    })
  ),
  operators: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string
    })
  )
};

const FilterCriteriaRow = ({ row, onChange, onRemove, docPrefixedFields, operators }) => {
  const [value, setValue] = useState();

  useEffect(() => setValue(row.value), [row.value]);

  return (
    <li className="slds-grid slds-gutters_small">
      <FieldsAndOperators
        docPrefixedFields={docPrefixedFields}
        operators={operators}
        fieldName={row.field_name}
        operator={row.operator}
        onChangeField={fieldName => onChange(mutateRow(row, "field_name", fieldName))}
        onChangeOperator={operator => onChange(mutateRow(row, "operator", operator))}
      />
      <div className="slds-col slds-size_1-of-5">
        <Input
          label="Value"
          value={value}
          required
          onChange={e => setValue(e.target.value)}
          onBlur={e => onChange(mutateRow(row, "value", value))}
        />
      </div>
      <RepeatingRemoveRowButton onRemove={onRemove} />
    </li>
  );
};

FilterCriteriaRow.proptype = propTypes;
FilterCriteriaRow.displayName = DEDUP_SCENARIO_FILTER_CRITERIAS;

export default withRepeating(FilterCriteriaRow);
