import React, { useState } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

import { Button } from "@salesforce/design-system-react";
import { Title } from "@salesforce/design-system-react/lib/components/page-header";

const propTypes = {
  children: PropTypes.node,
  isOpen: PropTypes.bool,
  nonCollapsible: PropTypes.bool,
  title: PropTypes.string.isRequired,
  className: PropTypes.string.isRequired
};

const ControlledExpandableSection = ({
  children,
  title,
  helpText,
  isOpen = false,
  nonCollapsible = false,
  className = "slds-m-bottom_medium slds-m-top_none"
}) => {
  const [_isOpen, setIsOpen] = useState(nonCollapsible ? true : isOpen);

  const titleNode = (
    <span
      className={classNames("slds-truncate", { "slds-p-horizontal_small": !!nonCollapsible })}
      title={title}
    >
      {title}
    </span>
  );

  const headerContainer = !nonCollapsible ? (
    <Button
      iconCategory="utility"
      iconClassName="slds-section__title-action-icon slds-button__icon_left"
      iconName="switch"
      onClick={() => setIsOpen(!_isOpen)}
      className="slds-section__title-action"
      variant="base"
    >
      {titleNode}
    </Button>
  ) : (
    titleNode
  );

  return (
    <div className={classNames(className, "slds-section", { "slds-is-open": _isOpen })}>
      <h3 className={classNames("slds-section__title", { "slds-theme_shade": !!nonCollapsible })}>
        {headerContainer}
      </h3>
      <div className="slds-section__content">{children}</div>
    </div>
  );
};

ControlledExpandableSection.propTypes = propTypes;

export default ControlledExpandableSection;
