import {
  SALESFORCE_LOGIN_SFDC,
  SALESFORCE_LOGIN_OAUTH,
  SALESFORCE_CONNECTION_NAME,
} from "./consts";

import { objHasCollectionOfProps } from "../utils";

export const openOAuthWindow = (url, w = 912, h = 513) => {
  // eslint-disable-next-line no-restricted-globals
  const left = screen.width / 2 - w / 2;
  // eslint-disable-next-line no-restricted-globals
  const top = screen.height / 2 - h / 2;
  const dimension = `width=${w},height=${h}`;
  const position = `top=${top},left=${left}`;
  const windowFeatures = `location=yes,toolbar=no,status=no,menubar=no, ${dimension}, ${position}`;
  return window.open(url, null, windowFeatures);
};

export const toggleCrosswindowMessageListener = (handleMessage) => {
  if (typeof handleMessage === "function") {
    if (window.addEventListener) {
      window.addEventListener("message", handleMessage, true);
    } else if (window.attachEvent) {
      window.attachEvent("message", handleMessage, true);
    }
  } else if (window.removeEventListener) {
    window.removeEventListener("message", null);
  } else if (window.detachEvent) {
    window.detachEvent("message", null);
  }
};

export const isOAuthCredential = (credential, strictChecking = false) => {
  const mandatoryFields = ["graxOAuthURL", "organizationId", "url", "name", "userName"];
  if (objHasCollectionOfProps(credential, [mandatoryFields.shift()])) {
    if (strictChecking === true && !objHasCollectionOfProps(credential, mandatoryFields)) {
      return false;
    }
    return true;
  }
  return false;
};

export const isSalesforceCredential = (credential, strictChecking = false) => {
  const mandatoryFields = ["userName", "password", "url", "organizationId", "name"];
  if (!isOAuthCredential(credential)) {
    if (
      strictChecking === true &&
      (!objHasCollectionOfProps(credential, mandatoryFields) ||
        credential.provider !== SALESFORCE_LOGIN_SFDC)
    ) {
      return false;
    }
    return true;
  }
  return false;
};

export const isValidSalesforceCredential = (credential, strictChecking = false) => {
  if (
    isOAuthCredential(credential, strictChecking) ||
    isSalesforceCredential(credential, strictChecking)
  ) {
    return true;
  }
  return false;
};

export const setDataSourceInitialState = (dataSourceType, name = SALESFORCE_CONNECTION_NAME) => ({
  [dataSourceType]: {
    name,
    provider: SALESFORCE_LOGIN_SFDC,
    [SALESFORCE_LOGIN_SFDC]: {
      type: dataSourceType,
    },
    [SALESFORCE_LOGIN_OAUTH]: {
      type: dataSourceType,
    },
  },
});
