import React, { Fragment } from "react";
import PropTypes from "prop-types";

import {
  SALESFORCE_GIT_INTEGRATION_GITHUB,
  SALESFORCE_GIT_INTEGRATION_GITLAB
} from "../../../../../consts/configurations/sfdcGitIntegration";
import StatefulInput from "../../../../commons/StatefulInput";
import GRAXButtonGroup from "../../../../commons/ButtonGroup";
import useGitIntegration from "../../../../../hooks/configuration/useGitIntegration";

const propTypes = {
  sourceControlProvider: PropTypes.oneOf([
    SALESFORCE_GIT_INTEGRATION_GITHUB,
    SALESFORCE_GIT_INTEGRATION_GITLAB
  ]).isRequired,
  gitRepositoryName: PropTypes.string.isRequired,
  gitRepoAuthorizationToken: PropTypes.string.isRequired,
  gitUserId: PropTypes.string.isRequired,
  handleButtonGroup: PropTypes.func.isRequired,
  onBlur: PropTypes.func.isRequired
};

const GitIntegration = ({
  sourceControlProvider,
  gitRepositoryName,
  gitRepoAuthorizationToken,
  gitUserId,
  handleButtonGroup,
  onBlur
}) => {
  const [{ options }] = useGitIntegration();

  return (
    <Fragment>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <GRAXButtonGroup
            options={options}
            value={sourceControlProvider}
            id="sourceControlProvider"
            onClick={handleButtonGroup}
          />
        </div>
      </div>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="Git Repository Name"
            required
            value={gitRepositoryName}
            id="gitRepositoryName"
            onBlur={onBlur}
          />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="Git Repository Authorization Token"
            helpText="In Github you should be able to find by navigating to:
            user>settings>developer settings>personal access token>generate token"
            required
            secured
            value={gitRepoAuthorizationToken}
            id="gitRepoAuthorizationToken"
            onBlur={onBlur}
          />
        </div>
        {sourceControlProvider === SALESFORCE_GIT_INTEGRATION_GITHUB && (
          <div className="slds-col slds-size_1-of-3">
            <StatefulInput
              label="Git User ID"
              required
              value={gitUserId}
              id="gitUserId"
              onBlur={onBlur}
            />
          </div>
        )}
      </div>
    </Fragment>
  );
};

GitIntegration.propTypes = propTypes;

export default GitIntegration;
