import React, { Fragment } from "react";

//TODO: should name be plural or singular
const DuplicateRowVertical = ({ field, record }) => {
  const getColumn = (field, record) => {
    return record[field];
  };

  return (
    <tr className="slds-hint-parent">
      <Fragment>
        <td>
          <div className="slds-truncate" title="Acme">
            {field}
          </div>
        </td>
        <td colSpan="3">
          <div className="slds-truncate" title="Acme">
            {getColumn(field, record)}
          </div>
        </td>
      </Fragment>
    </tr>
  );
};

//TODO: set proptypes

export default DuplicateRowVertical;
