import React from "react";

import { Combobox, Icon, comboboxFilterAndLimit } from "@salesforce/design-system-react";

class SCombobox extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      inputValue: "",
      selection: []
    };
  }

  componentDidMount() {
    this.setState({ selection: this.props.selection });
  }

  componentDidUpdate(prevProps) {
    if (this.props.value !== prevProps.value) {
      this.setState({ selection: this.props.selection });
    }
  }

  render() {
    return (
      <Combobox
        events={{
          onChange: (event, { value }) => {
            this.props.onSelect(event, value);
            this.setState({ inputValue: value });
          },
          onRequestRemoveSelectedOption: (event, data) => {
            this.setState({
              inputValue: "",
              selection: []
            });
            this.props.onSelect(event, data.selection);
          },
          onSubmit: (event, { value }) => {
            this.props.onSelect(event, value);
            this.setState({
              inputValue: "",
              selection: [
                ...this.state.selection,
                {
                  label: value,
                  icon: (
                    <Icon assistiveText={{ label: "Account" }} category="standard" name="account" />
                  )
                }
              ]
            });
          },
          onSelect: (event, data) => {
            this.props.onSelect(event, ...Object.keys(data).map(key => data[key]));
            this.setState({
              inputValue: "",
              selection: data.selection
            });
          }
        }}
        labels={this.props.labels}
        options={comboboxFilterAndLimit({
          inputValue: this.state.inputValue,
          options: this.props.options,
          selection: this.state.selection || [],
          limit: 1000
        })}
        selection={this.state.selection}
        value={this.state.selectedOption ? this.state.selectedOption.label : this.state.inputValue}
        variant="inline-listbox"
        required
        menuItemVisibleLength={5}
      />
    );
  }
}

SCombobox.displayName = "SCombobox";

export default SCombobox;
