import { useEffect } from "react";

import { ADVANCED_CONFIG_SET_VALUE } from "../../consts/configurations/configurationsActions";

import { useSyncAnalyticsContext } from "../../contexts/syncAnalyticsContext";
import { useHandleInput, useHandleCheckbox, useFormDispatch } from "../useHandleForm";

const useSyncAnalytics = syncSalesforceEinsteinOrganizationId => {
  const [state, dispatch] = useSyncAnalyticsContext();
  const onBlur = useHandleInput(ADVANCED_CONFIG_SET_VALUE, dispatch);
  const handleCheckbox = useHandleCheckbox(ADVANCED_CONFIG_SET_VALUE, dispatch);
  const enableGlobalDeault = useFormDispatch(ADVANCED_CONFIG_SET_VALUE, dispatch);

  const {
    marketingCloudHost,
    marketingCloudUser,
    marketingCloudPassword,
    marketingCloudPort,
    syncQueryFile,
    syncBatchSize,
    syncDestinationAnalyticsUrl,
    isEinsteinAnalyticsEnabled,
    isMarketingCloudEnabled
  } = state;

  useEffect(() => {
    if (syncSalesforceEinsteinOrganizationId !== undefined)
      enableGlobalDeault("isEinsteinAnalyticsEnabled", true);
  }, [syncSalesforceEinsteinOrganizationId]);

  useEffect(() => {
    if (marketingCloudHost !== undefined) enableGlobalDeault("isMarketingCloudEnabled", true);
  }, [marketingCloudHost]);

  return [
    {
      isEinsteinAnalyticsEnabled,
      isMarketingCloudEnabled,
      marketingCloudHost,
      marketingCloudUser,
      marketingCloudPassword,
      marketingCloudPort,
      syncQueryFile,
      syncBatchSize,
      syncDestinationAnalyticsUrl
    },
    { onBlur, handleCheckbox }
  ];
};

export default useSyncAnalytics;
