import _ from "lodash";

import { isAnExistingConfiguration, omitEmptyProps, getFirstEnvironment } from ".";

import {
  PUSHTOQUEUE_PROVIDER_APIGEE,
  PUSHTOQUEUE_PROVIDER_KAFKA,
} from "../advancedConfiguration/environmentVariables/consts";
import { METADABACKUP_GITPROVIDER_GITLAB } from "../advancedConfiguration/metadataBackupSettings/consts";
import { isOAuthCredential } from "../salesforceConnection/utils";

export const preValidateDataSourcesSchema = (dataSources) => {
  if (_.isArray(dataSources)) {
    return dataSources
      .map((dataSource) => {
        if (isOAuthCredential(dataSource)) {
          const { provider, ...credential } = dataSource;
          return omitEmptyProps(credential);
        }

        return omitEmptyProps(dataSource);
      })
      .filter((dataSource) => {
        if (_.isPlainObject(dataSource)) {
          const { type, provider, organizationId, name, ...mandatoryProps } = dataSource;
          return Object.keys(mandatoryProps).length > 0;
        }

        return false;
      });
  }

  return [];
};

const preValidateMetadataBackupSchema = (metadataBackup) => {
  const { isEnabled, sourceControlEnabled, sourceControlProvider } = metadataBackup;
  let schema;

  if (!isEnabled || !sourceControlEnabled) {
    schema = { isEnabled: !!isEnabled, sourceControlEnabled: false };
  } else if (sourceControlProvider === METADABACKUP_GITPROVIDER_GITLAB) {
    const { gitUserId, ...omittedGitUserId } = metadataBackup;
    schema = omittedGitUserId;
  } else {
    schema = metadataBackup;
  }

  return schema;
};

const preValidatePushToQueueSchema = (pushToQueue) => {
  let schema = {};

  if (pushToQueue.provider === PUSHTOQUEUE_PROVIDER_KAFKA) {
    const { provider, authorizationToken, apiEndpoint } = pushToQueue;
    schema = { provider, authorizationToken, apiEndpoint };
  } else if (pushToQueue.provider === PUSHTOQUEUE_PROVIDER_APIGEE) {
    const { provider, apiClientId, apiEndpoint, apiClientSecret } = pushToQueue;
    schema = { provider, apiClientId, apiEndpoint, apiClientSecret };
  }

  return schema;
};

export const preValidateVariablesSchema = (variables) => {
  let schema = variables;

  if (variables.cdcEnabled !== undefined) {
    schema = {
      ...schema,
      cdcEnabled: variables.cdcEnabled.toString(),
    };
  }

  return schema;
};

export const preValidatesyncAnalyticsSchema = ({
  isEinsteinAnalyticsEnabled,
  isMarketingCloudEnabled,
  ...syncAnalytics
}) => {
  let schema = { ...syncAnalytics };
  if (isEinsteinAnalyticsEnabled === undefined || isEinsteinAnalyticsEnabled === false) {
    const {
      syncEinsteinAnalyticsDatasetName,
      syncSalesforceObjects,
      ...omittedEinsteinAnalytics
    } = syncAnalytics;

    schema = omittedEinsteinAnalytics;
  }

  if (isMarketingCloudEnabled === false) {
    const {
      marketingCloudHost,
      marketingCloudUser,
      marketingCloudPassword,
      marketingCloudPort,
      ...omittedMMC
    } = syncAnalytics;
    schema = omittedMMC;
  }

  return schema;
};

const environmentSchemas = {
  storage: (schema) => schema,
  oauth: (schema) => schema,
  elastic: (schema) => schema,
  timeMachine: (schema) => schema,
  asyncIntervalMinutes: (schema) => schema,
  sessionCacheTimeoutMinutes: (schema) => schema,
  syncAnalytics: preValidatesyncAnalyticsSchema,
  dataSources: preValidateDataSourcesSchema,
  metadataBackup: preValidateMetadataBackupSchema,
  pushToQueue: preValidatePushToQueueSchema,
  variables: preValidateVariablesSchema,
};

export const environmentProxy = (environment, isSingleEnvironment = true) => {
  let target = {};

  if (isAnExistingConfiguration(environment)) {
    target = _.cloneDeep(getFirstEnvironment(environment));
  }

  return new Proxy(target, {
    get(obj, prop) {
      if (prop in obj) {
        if (prop === "dataSources" || _.isPlainObject(obj[prop]) === true) {
          return obj[prop];
        }
        if (Array.isArray(obj[prop]) === true) {
          return isSingleEnvironment === true ? obj[prop][0] : [obj[prop][0]];
        }
      }

      return null;
    },
    set(obj, prop, value) {
      if (environmentSchemas[prop]) {
        const newValue = omitEmptyProps(environmentSchemas[prop](value));
        // eslint-disable-next-line no-param-reassign
        obj[prop] = prop === "variables" || prop === "dataSources" ? newValue : [newValue];
        return true;
      }

      throw Error(`${prop} is not an existing configuration's node`);
    },
  });
};

const globalEnvironmentDefaultsKeys = [
  "asyncAttachmentProcessorOrganizationIds",
  "asyncBulkLoadOrganizationIds",
  "asyncQueueOrganizationIds",
  "bulkRestoreDestinationOrganizationId",
  "bulkRestoreSourceOrganizationId",
  "syncSalesforceEinsteinOrganizationIds",
  "syncSalesforceSourceOrganizationIds",
  "metadataBackupOrganizationIds",
  "odataOrganizationIds",
  "asyncIntervalMinutes",
  "sessionCacheTimeoutMinutes",
  "configRetrievalIntervalMinutes",
];

export const globalEnvironmentDefaultsProxy = (configuration, isSingleEnvironment = true) => {
  const target = isAnExistingConfiguration(configuration)
    ? _.cloneDeep(configuration.globalEnvironmentDefaults)
    : {};

  const isValidGlobalDefault = (gDefault) =>
    _.isString(gDefault) || _.isNumber(gDefault) || _.isBoolean(gDefault);

  return new Proxy(target, {
    get(obj, prop) {
      if (!globalEnvironmentDefaultsKeys.includes(prop)) {
        throw Error(`${prop} is not a valid global default key`);
      }

      if (prop === "raw") {
        return { globalEnvironmentDefaults: omitEmptyProps(target) };
      }

      if (prop in obj) {
        if (prop.endsWith("Ids") && isSingleEnvironment === true) {
          return obj[prop][0] || null;
        }
        return obj[prop];
      }

      return null;
    },
    set(obj, prop, value) {
      if (!globalEnvironmentDefaultsKeys.includes(prop)) {
        throw Error(`${prop} is not a valid global default key`);
      }

      if (prop.endsWith("Ids")) {
        if (!_.isArray(value)) {
          throw Error(`${prop} should be a collection of 1 more more keys`);
        }
        if (value.length > 0 && value.filter((gGlobal) => !_.isString(gGlobal)).length > 0) {
          throw Error(`${value} is not a valid global default value for ${prop}`);
        }
      } else if (!isValidGlobalDefault(value)) {
        throw Error(`${value} is not a valid global default value for ${prop}`);
      }

      // eslint-disable-next-line no-param-reassign
      obj[prop] = value;

      return true;
    },
  });
};
