import React, { Fragment } from "react";

import { Modal, IconSettings } from "@salesforce/design-system-react";
import DuplicateRowVertical from "./duplicateRowVertical";

const DuplicatesExpanded = ({ setIsOpen, tableFields, record }) => {
  return (
    <IconSettings iconPath="/assets/icons">
      <Modal isOpen={true} size="medium" onRequestClose={() => setIsOpen(false)} hasNoHeader={true}>
        <table
          aria-multiselectable="true"
          className="slds-table slds-table_header-hidden slds-table_bordered slds-table_cell-buffer slds-no-row-hover slds-tree slds-table_tree"
          role="grid"
        >
          <thead>
            <tr aria-level="1" className="slds-hint-parent">
              <th role="gridcell">
                <div className="slds-truncate" title="Acme">
                  STANDARD FIELD NAMES
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            {record !== null && (
              <Fragment>
                {tableFields.map(field => (
                  <DuplicateRowVertical field={field} record={record} />
                ))}
              </Fragment>
            )}
          </tbody>
        </table>
      </Modal>
    </IconSettings>
  );
};

export default DuplicatesExpanded;
