import React from "react";

import { ExpandableSection, Button } from "@salesforce/design-system-react";
import ProviderFactory from "./ProviderFactory";
import GRAXButtonGroup from "../../../../commons/ButtonGroup";
import useCloudStorageForm from "../../../../../hooks/configuration/useCloudStorageForm";
import VendorValidityMessage from "../../../../commons/VendorValidityMessage";
import useIndividualVendorValidity, {
  useAutoDismissToast,
} from "../../../../../hooks/configuration/useIndividualVendorValidity";

const CloudStorageForm = () => {
  const [validation, validateVendor] = useIndividualVendorValidity();
  const isOpen = useAutoDismissToast(validation);
  const [
    { selectedTab, provider, options },
    { handleButtonGroup, dispatch },
  ] = useCloudStorageForm();

  const onValidation = () => {
    const payload = {
      type: "storage",
      credentials: provider,
    };

    validateVendor(payload);
  };

  return (
    <ExpandableSection
      title="Cloud Storage Provider"
      className="slds-m-bottom_medium slds-m-top_none"
      nonCollapsible
    >
      {isOpen === true && <VendorValidityMessage variant="storage" status={validation.status} />}
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_2-of-3">
          <GRAXButtonGroup
            options={options}
            value={selectedTab}
            id="provider"
            onClick={handleButtonGroup}
          />
        </div>
      </div>
      <ProviderFactory selectedTab={selectedTab} {...provider} dispatch={dispatch} />
      <div className="slds-grid slds-gutters_small slds-m-top_small">
        <div className="slds-col slds-size_1-of-3">
          <Button label="Validate Storage Connection" onClick={onValidation} />
        </div>
      </div>
    </ExpandableSection>
  );
};

export default CloudStorageForm;
