import React from "react";
import PropTypes from "prop-types";

import HeaderButtons from "../HeaderButtons";
import useSaveAdvancedConfiguration from "../../../../hooks/configuration/useSaveAdvancedConfiguration";

const propTypes = {
  formRef: PropTypes.shape({ current: PropTypes.object }).isRequired,
  isAnExistingConfig: PropTypes.bool.isRequired
};

const HeaderActions = ({ isAnExistingConfig }) => {
  const onSubmit = useSaveAdvancedConfiguration();
  return <HeaderButtons isAnExistingConfig={isAnExistingConfig} onSubmit={onSubmit} />;
};

HeaderActions.propTypes = propTypes;
export default HeaderActions;
