import React from "react";
import PropTypes from "prop-types";
import { Button } from "@salesforce/design-system-react";

import useCloudRoutes from "../../../hooks/useCloudRoutes";

const propTypes = {
  // eslint-disable-next-line react/require-default-props
  formRef: PropTypes.shape({ current: PropTypes.object }),
  onSubmit: PropTypes.func.isRequired,
  isAnExistingConfig: PropTypes.bool.isRequired
};

const HeaderButtons = ({ isAnExistingConfig, formRef, onSubmit }) => {
  const { goToStartProvisioning, goToSummary } = useCloudRoutes();
  const goBack = () => (isAnExistingConfig === true ? goToSummary : goToStartProvisioning)();

  const handleSubmit = () => {
    if (formRef !== undefined) {
      if (formRef.current.checkValidity() === true) onSubmit();
    } else {
      onSubmit();
    }
  };

  return (
    <div className="slds-grid slds-m-bottom_medium" style={{ justifyContent: "flex-end" }}>
      <Button
        className="slds-m-right_x-small"
        label="Save"
        variant="brand"
        type={formRef !== undefined ? "submit" : "button"}
        onClick={handleSubmit}
      />
      <Button label="Cancel" onClick={goBack} />
    </div>
  );
};

HeaderButtons.propTypes = propTypes;

export default HeaderButtons;
