import React from "react";
import PropTypes from "prop-types";

import StatefulInput from "../../../../commons/StatefulInput";

const propTypes = {
  label: PropTypes.string.isRequired,
  variableName: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  onBlur: PropTypes.func.isRequired
};

const ProtectedInput = ({ label, variableName, type, value, onBlur, ...props }) => {
  const handleNonCastingOnblur = e => onBlur(e, { valueCasting: false });

  return (
    <div className="slds-col slds-size_1-of-3">
      <StatefulInput
        label={label}
        id={variableName}
        type={type}
        value={value}
        onBlur={handleNonCastingOnblur}
        valueCasting
        {...props}
      />
    </div>
  );
};

ProtectedInput.propTypes = propTypes;

export default ProtectedInput;
