import { QUICK_START_FORM_INITIAL_LOAD } from "../../consts/configurations/configurationsActions";
import { CLOUD_STORAGE_PROVIDERS_S3_COMPATIBLE } from "../../consts/configurations/cloudStorageProviders";
import quickStartFormReducer from "./quickStartFormReducer";

const storageReducer = (state, { type, payload }) => {
  let newState = state;

  if (type === QUICK_START_FORM_INITIAL_LOAD) {
    if (payload.s3CompatibleEndpoint) {
      newState = {
        ...newState,
        provider: CLOUD_STORAGE_PROVIDERS_S3_COMPATIBLE,
      };
    }
  }

  return quickStartFormReducer(newState, { type, payload });
};

export default storageReducer;
