import React, { useState, Fragment, useEffect } from "react";
import { PageHeader, Card } from "@salesforce/design-system-react";

//TODO: import component name
import DuplicatesHeader from "./duplicatesHeader";
import DuplicateRow from "./duplicateRow";
import useDuplicates from "../../../../hooks/dedup/useDuplicates";
import DuplicateKey from "./DuplicateKey";
import DuplicatesExpanded from "./duplicatesVertical";

const Duplicates = () => {
  const { tableFields, tableFieldsExpanded, rows, indexName } = useDuplicates();

  const [isOpen, setIsOpen] = useState(false);
  const [selectedRecord, setSelectedRecord] = useState(null);

  const onViewRecord = row => {
    setSelectedRecord(row);
  };

  useEffect(() => {
    if (selectedRecord !== null) setIsOpen(true);
  }, [selectedRecord]);

  return (
    <React.Fragment>
      <PageHeader
        className="slds-m-bottom_large"
        title="All Potential Duplicate Records"
        variant="object-home"
        trail={[<a href="javascript:void(0);">Dedup</a>]}
      />
      <Card
        style={{ flexGrow: 1 }}
        hasNoHeader={indexName === null}
        heading={`Index: ${indexName}`}
      >
        {rows.length > 0 && (
          <Fragment>
            <table
              aria-multiselectable="true"
              className="slds-table  slds-table_bordered slds-no-row-hover "
              role="grid"
            >
              <DuplicatesHeader tableFields={tableFields} />
              <tbody>
                {rows.map(row => (
                  <Fragment>
                    <DuplicateKey keyName={row.key} tableFields={tableFields} />
                    {row.records.map(record => (
                      <DuplicateRow
                        tableFields={tableFields}
                        row={record}
                        onViewRecord={onViewRecord}
                      />
                    ))}
                  </Fragment>
                ))}
              </tbody>
            </table>
            {isOpen === true && (
              <DuplicatesExpanded
                isOpen={isOpen}
                setIsOpen={setIsOpen}
                tableFields={tableFieldsExpanded}
                record={selectedRecord}
              />
            )}
          </Fragment>
        )}
      </Card>
    </React.Fragment>
  );
};
export default Duplicates;
