import { createContext } from "react";

import { withQuickStartProvider } from "./contextProviders";
import storageReducer from "../reducers/configurations/storageReducer";
import { useStateContext, useDispatchContext } from "../hooks/configuration/useContextProvider";

import {
  CLOUD_STORAGE_PROVIDERS_AWS_S3,
  CLOUD_STORAGE_PROVIDERS_S3_COMPATIBLE,
  CLOUD_STORAGE_PROVIDERS_AZURE,
  CLOUD_STORAGE_PROVIDERS_GOOGLE_CLOUD_PLATFORM,
  CLOUD_STORAGE_PROVIDERS_MINIO
} from "../consts/configurations/cloudStorageProviders";

const initialState = {
  provider: CLOUD_STORAGE_PROVIDERS_AWS_S3,
  [CLOUD_STORAGE_PROVIDERS_AWS_S3]: {},
  [CLOUD_STORAGE_PROVIDERS_S3_COMPATIBLE]: {},
  [CLOUD_STORAGE_PROVIDERS_AZURE]: {},
  [CLOUD_STORAGE_PROVIDERS_GOOGLE_CLOUD_PLATFORM]: {},
  [CLOUD_STORAGE_PROVIDERS_MINIO]: {}
};

const stateContext = createContext();
const dispatchContext = createContext();

export const useCloudStorageContext = () => [
  useStateContext(stateContext),
  useDispatchContext(dispatchContext)
];

export const CloudStorageProvider = withQuickStartProvider(
  stateContext,
  dispatchContext,
  initialState,
  storageReducer
);
