import PropTypes from "prop-types";
import React from "react";

import AwsRegions from "../../../../commons/AwsRegions";
import StatefulCheckbox from "../../../../commons/StatefulCheckbox";
import StatefulInput from "../../../../commons/StatefulInput";
import AssumeRoleOptions from "./AssumeRoleOptions";
import ServerSideEncryption from "./ServerSideEncryption";

const propTypes = {
  accessKeyName: PropTypes.string,
  secretAccessKey: PropTypes.string,
  bucket: PropTypes.string,
  region: PropTypes.string,
  // eslint-disable-next-line react/require-default-props
  s3AssumeRoleEnabled: PropTypes.bool,
  s3AssumeRoleArn: PropTypes.string,
  s3AssumeRoleExternalId: PropTypes.string,
  s3AssumeRoleRefreshCredsInHour: PropTypes.number,
  serverSideEncryptionKmsKey: PropTypes.string,
  serverSideEncryption: PropTypes.string,
  onBlur: PropTypes.func.isRequired,
  handleButtonGroup: PropTypes.func.isRequired,
  handleCheckbox: PropTypes.func.isRequired,
  handleCombobox: PropTypes.func.isRequired,
};

const defaultProps = {
  accessKeyName: "",
  secretAccessKey: "",
  bucket: "",
  region: "",
  s3AssumeRoleArn: "",
  s3AssumeRoleExternalId: "",
  s3AssumeRoleRefreshCredsInHour: 12,
  serverSideEncryptionKmsKey: "",
  serverSideEncryption: "",
};

const AwsS3Storage = ({
  accessKeyName,
  secretAccessKey,
  bucket,
  region,
  s3AssumeRoleEnabled,
  s3AssumeRoleArn,
  s3AssumeRoleRefreshCredsInHour,
  s3AssumeRoleExternalId,
  serverSideEncryptionKmsKey,
  serverSideEncryption,
  onBlur,
  handleButtonGroup,
  handleCheckbox,
  handleCombobox,
}) => {
  return (
    <>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="S3 Bucket Name"
            required
            value={bucket}
            id="bucket"
            onBlur={onBlur}
          />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <AwsRegions
            label="S3 Region"
            value={region}
            id="region"
            onChange={(selections) => handleCombobox("region", selections)}
          />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="Access Key ID"
            required
            secured
            value={accessKeyName}
            id="accessKey"
            onBlur={onBlur}
          />
        </div>
      </div>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="Secret Access Key"
            required
            secured
            value={secretAccessKey}
            id="secretAccessKey"
            onBlur={onBlur}
          />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulCheckbox
            label="Assume Role Enabled"
            checked={s3AssumeRoleEnabled}
            id="s3AssumeRoleEnabled"
            variant="toggle"
            isDestructive
            onToggle={handleCheckbox}
          />
        </div>
      </div>
      {s3AssumeRoleEnabled === true && (
        <AssumeRoleOptions
          s3AssumeRoleArn={s3AssumeRoleArn}
          s3AssumeRoleRefreshCredsInHour={s3AssumeRoleRefreshCredsInHour}
          s3AssumeRoleExternalId={s3AssumeRoleExternalId}
          onBlur={onBlur}
        />
      )}
      <ServerSideEncryption
        serverSideEncryptionKmsKey={serverSideEncryptionKmsKey}
        serverSideEncryption={serverSideEncryption}
        onBlur={onBlur}
        handleButtonGroup={handleButtonGroup}
      />
    </>
  );
};

AwsS3Storage.propTypes = propTypes;
AwsS3Storage.defaultProps = defaultProps;

export default AwsS3Storage;
