import React, { Fragment } from "react";

import { ToastContainer, Spinner, Toast } from "@salesforce/design-system-react";
import useInteractiveFetchStatus from "../../hooks/useInteractiveFetchStatus";

const InteractiveFetchStatus = () => {
  const [
    { isOpen, isLoading, variant, message = {}, duration },
    { hideNotification }
  ] = useInteractiveFetchStatus();

  return (
    <Fragment>
      {isLoading && <Spinner size="large" variant="brand" />}
      {isOpen === true && (
        <ToastContainer>
          <Toast
            duration={duration}
            labels={message}
            variant={variant}
            onRequestClose={hideNotification}
          />
        </ToastContainer>
      )}
    </Fragment>
  );
};

export default InteractiveFetchStatus;
