/* eslint-disable react/require-default-props */
import React from "react";
import PropTypes from "prop-types";

import { Button } from "@salesforce/design-system-react";

import {
  SALESFORCE_OAUTH_URL,
  SALESFORCE_LOGIN_PRODUCTIONURL,
  SALESFORCE_LOGIN_SANDBOXURL,
} from "./consts";

import { retrieveEnvironmentCredentials } from "../../../../lib/api";
import { openOAuthWindow } from "./utils";

const propTypes = {
  label: PropTypes.string,
  loginurl: PropTypes.oneOf([SALESFORCE_LOGIN_PRODUCTIONURL, SALESFORCE_LOGIN_SANDBOXURL]),
  urlRef: PropTypes.oneOfType([PropTypes.func, PropTypes.shape({ current: Element })]),
};

const engagementGraph = retrieveEnvironmentCredentials();
const appendQueryParameter = (loginurl) =>
  `${SALESFORCE_OAUTH_URL}?gateway-token=${engagementGraph.gatewayToken}&api-token=${engagementGraph.apiToken}&loginurl=${loginurl}`;

let oauthwindowInstance = null;
let previousLoginURL = null;

export const initiateOauthFlow = (oauthUrl) => {
  if (!oauthwindowInstance || oauthwindowInstance.closed || previousLoginURL !== oauthUrl) {
    oauthwindowInstance = openOAuthWindow(appendQueryParameter(oauthUrl));
    previousLoginURL = oauthUrl;
  } else {
    oauthwindowInstance.focus();
  }
};

const OAuthFlowWindow = ({ label, loginurl, urlRef }) => {
  const handleButton = () => {
    if (urlRef && urlRef.reportValidity()) {
      initiateOauthFlow(loginurl);
    }
  };

  return <Button label={label} variant="outline-brand" onClick={handleButton} />;
};

OAuthFlowWindow.propTypes = propTypes;

export default OAuthFlowWindow;
