import React from "react";
import PropTypes from "prop-types";
import StatefulInput from "../../../../commons/StatefulInput";

const propTypes = {
  url: PropTypes.string,
  gatewayPath: PropTypes.string,
  onBlur: PropTypes.func.isRequired
};

const BonsaiElasticsearch = ({ url, gatewayPath, onBlur }) => {
  return (
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput label="Bonsai URL" required value={url} id="url" onBlur={onBlur} />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput label="Gateway path" value={gatewayPath} id="gatewayPath" onBlur={onBlur} />
      </div>
    </div>
  );
};

BonsaiElasticsearch.propTypes = propTypes;

export default BonsaiElasticsearch;
