import REGION_LABEL_MAP from "../../consts/configurations/regionLabelMap";

const options = Object.entries(REGION_LABEL_MAP).map(([value, label], index) => ({
  label,
  value,
  subTitle: value,
  id: index.toString()
}));

export const getRegionLabel = region => REGION_LABEL_MAP[region];

const useAwsRegions = () => {
  const getSelection = region => {
    const selection = options.find(option => option.value === region);
    if (selection !== undefined) return [selection];
    return undefined;
  };

  return [options, getSelection];
};

export default useAwsRegions;
