import React from "react";
import PropTypes from "prop-types";

import { ExpandableSection } from "@salesforce/design-system-react";

import StatefulInput from "../../../../commons/StatefulInput";
import EinsteinSalesforceDestination from "./EinsteinSalesforceDestination";
import MarketingCloudSettings from "./MarketingCloudSettings";
import useSyncAnalytics from "../../../../../hooks/configuration/useSyncAnalytics";
import StatefulCheckbox from "../../../../commons/StatefulCheckbox";

const propTypes = {
  syncSalesforceEinsteinOrganizationId: PropTypes.bool.isRequired
};

const SyncAnalytics = ({ syncSalesforceEinsteinOrganizationId }) => {
  const [
    {
      isEinsteinAnalyticsEnabled,
      isMarketingCloudEnabled,
      marketingCloudHost,
      marketingCloudUser,
      marketingCloudPassword,
      marketingCloudPort,
      syncQueryFile,
      syncBatchSize
    },
    { onBlur, handleCheckbox }
  ] = useSyncAnalytics(syncSalesforceEinsteinOrganizationId);

  return (
    <ExpandableSection title="Data Sync" className="slds-m-bottom_medium slds-m-top_none">
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulCheckbox
            label="Einstein Analytics"
            checked={isEinsteinAnalyticsEnabled}
            id="isEinsteinAnalyticsEnabled"
            isDestructive
            variant="toggle"
            onToggle={handleCheckbox}
          />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulCheckbox
            label="Marketing Cloud"
            checked={isMarketingCloudEnabled}
            id="isMarketingCloudEnabled"
            isDestructive
            variant="toggle"
            onToggle={handleCheckbox}
          />
        </div>
      </div>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="Query File"
            helpText="Please ensure the query file is a json file and .json is included in the file name you enter here.
            This json configuration query file must be stored in
             the following path of your cloud storage providerExample
            Path: Amazon S3 > Vandelay Industries
            Prod Bucket > grax > audittrail > salesforce >
            Org ID of Salesforce Environment > configurationqueryfile.json"
            required
            value={syncQueryFile}
            id="syncQueryFile"
            onBlur={onBlur}
          />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="Batch Size"
            type="number"
            value={syncBatchSize}
            id="syncBatchSize"
            onBlur={onBlur}
          />
        </div>
      </div>
      {isEinsteinAnalyticsEnabled === true && <EinsteinSalesforceDestination />}
      {isMarketingCloudEnabled && (
        <MarketingCloudSettings
          marketingCloudHost={marketingCloudHost}
          marketingCloudUser={marketingCloudUser}
          marketingCloudPassword={marketingCloudPassword}
          marketingCloudPort={marketingCloudPort}
          onBlur={onBlur}
        />
      )}
    </ExpandableSection>
  );
};

SyncAnalytics.propTypes = propTypes;

export default SyncAnalytics;
