import React from "react";
import PropTypes from "prop-types";

import { Checkbox } from "@salesforce/design-system-react";

import { DEDUP_SCENARIO_MATCH_CRITERIAS } from "../../../consts/dedup/componentNames";
import { mutateRow } from "../../../lib/utils";
import { RepeatingRemoveRowButton } from "../../commons/Buttons";
import withRepeating from "../../commons/withRepeating";
import FieldsAndOperators from "./FieldsAndOperators";

const propTypes = {
  row: PropTypes.shape({
    field: PropTypes.string,
    operator: PropTypes.string,
    value: PropTypes.string,
    stopWord: PropTypes.array,
    synonyms: PropTypes.array,
    cleanse: PropTypes.bool
  }),
  index: PropTypes.number,
  onChange: PropTypes.func,
  onRemove: PropTypes.func,
  docPrefixedFields: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string
    })
  ),
  operators: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string
    })
  )
};

const MatchCriteriaRow = ({
  row,
  onChange,
  onRemove,
  setMetadata,
  docPrefixedFields,
  operators,
  rowsNum
}) => {
  const disabledRemoveButton = rowsNum === 1;
  return (
    <li className="slds-grid slds-gutters_small">
      <FieldsAndOperators
        docPrefixedFields={docPrefixedFields}
        operators={operators}
        fieldName={row.field_name}
        operator={row.operator}
        onChangeField={fieldName =>
          onChange(
            Object.assign({}, row, {
              field_name: fieldName,
              operator: ""
            })
          )
        }
        onChangeOperator={operator => onChange(mutateRow(row, "operator", operator))}
      />
      <div className="slds-col slds-size_1-of-5">
        <div className="slds-form-element">
          <label className="slds-form-element__label">stopWord</label>
          <div className="slds-form-element__control">
            <a
              className=""
              style={{ lineHeight: "1.875rem" }}
              href={e => e.preventDefault()}
              onClick={() =>
                setMetadata({
                  matchCriteriaId: row.id,
                  id: row.stopwords_list_id,
                  variant: "stopwords"
                })
              }
            >
              Contact Prefixes
            </a>
          </div>
        </div>
      </div>
      <div className="slds-col slds-size_1-of-6">
        <div className="slds-form-element">
          <label className="slds-form-element__label">Synonym</label>
          <div className="slds-form-element__control">
            <a
              className=""
              style={{ lineHeight: "1.875rem" }}
              href={e => e.preventDefault()}
              onClick={() =>
                setMetadata({
                  matchCriteriaId: row.id,
                  id: row.synonyms_id,
                  variant: "synonyms"
                })
              }
            >
              Synonym name
            </a>
          </div>
        </div>
      </div>
      <div className="slds-col slds-size_2-of-12">
        <span className="slds-form-element__label">Use Cleansed Field</span>
        <div className="slds-form-element__control">
          <Checkbox
            variant="toggle"
            checked={row.cleanse}
            onChange={(event, { checked }) => onChange(mutateRow(row, "cleanse", !row.cleanse))}
          />
        </div>
      </div>
      <RepeatingRemoveRowButton onRemove={onRemove} disabled={disabledRemoveButton} />
    </li>
  );
};

MatchCriteriaRow.prototype = propTypes;
MatchCriteriaRow.displayName = DEDUP_SCENARIO_MATCH_CRITERIAS;

export default withRepeating(MatchCriteriaRow);
