import { createRef } from "react";

import { useConfigurationsContext } from "../../contexts/configurationsContext";
import CloudConfig from "../../models/CloudConfig";
import { isAnExistingConfiguration } from "../../components/cloud/configuration/utils";
import { globalEnvironmentDefaultsProxy } from "../../components/cloud/configuration/utils/configurationProxy";

const useAdvancedConfiguration = () => {
  const [state] = useConfigurationsContext();
  const formRef = createRef();

  const isAnExistingConfig = isAnExistingConfiguration(state);

  const cloudConfig = new CloudConfig(state);
  const globals = globalEnvironmentDefaultsProxy(state);

  const {
    asyncIntervalMinutes,
    sessionCacheTimeoutMinutes,
    storage,
    oauth,
    metadataBackup,
    syncAnalytics,
    variables,
    timeMachine,
    pushToQueue,
    syncSalesforceEinsteinOrganizationId,
    dataSources,
  } = cloudConfig;

  const environmentVariables = {
    asyncIntervalMinutes,
    sessionCacheTimeoutMinutes,
    variables,
    timeMachine,
    pushToQueue,
  };

  return {
    dataSources,
    globals,
    storage,
    oauth,
    metadataBackup,
    syncAnalytics,
    environmentVariables,
    syncSalesforceEinsteinOrganizationId,
    isAnExistingConfig,
    formRef,
  };
};

export default useAdvancedConfiguration;
