import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { DEDUP_SCENARIO_FIELDS_AND_OPERATORS } from "../../../consts/dedup/componentNames";
import GRAXCombobox from "../../commons/GRAXCombobox";
import { useOperators } from "../../../hooks/dedup/useFieldsAndOperators";

const propTypes = {
  fieldName: PropTypes.string,
  docPrefixedFields: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string
    })
  ),
  operator: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string
    })
  ),
  onChangeField: PropTypes.func,
  onChangeOperator: PropTypes.func,
  operators: PropTypes.arrayOf(PropTypes.string)
};

const FieldsAndOperators = ({
  fieldName,
  operator,
  docPrefixedFields,
  onChangeField,
  onChangeOperator
}) => {
  const [dropdownLabel, setDropdownLabel] = useState("");
  const [operators, setDataType] = useOperators(fieldName);

  useEffect(() => {
    if (operators.length === 0 && fieldName !== "" && docPrefixedFields.length > 0) {
      const fieldDataType = docPrefixedFields.find(field => field.value === fieldName);
      if (fieldDataType !== undefined) setDataType(fieldDataType.dataType);
    }
  }, [docPrefixedFields]); //TODO: watch for fieldName also

  useEffect(() => {
    let fieldLabel = "";
    if (fieldName !== undefined && fieldName.startsWith("doc."))
      fieldLabel = fieldName.split("doc.")[1];

    setDropdownLabel(fieldLabel);
  }, [fieldName]);

  const handleFieldName = selection => {
    if (selection.length === 1) {
      const { value, dataType } = selection[0];
      onChangeField(value);
      setDataType(dataType);
    } else {
      onChangeField("");
      setDataType(null);
    }
  };

  return (
    <React.Fragment>
      <div className="slds-col slds-size_1-of-5">
        <GRAXCombobox
          onChange={(event, selection) => handleFieldName(selection)}
          options={docPrefixedFields}
          value={dropdownLabel}
          required
          labels={{
            label: "Fields",
            placeholder: "Select Option"
          }}
        />
      </div>
      <div className="slds-col slds-size_1-of-5">
        <GRAXCombobox
          onChange={(event, selection) =>
            onChangeOperator(selection.length === 1 ? selection[0].label : "")
          }
          options={operators}
          value={operator}
          required
          labels={{
            label: "Operators",
            placeholder: "Select Option"
          }}
        />
      </div>
    </React.Fragment>
  );
};

FieldsAndOperators.prototype = propTypes;
FieldsAndOperators.displayName = DEDUP_SCENARIO_FIELDS_AND_OPERATORS;

export default FieldsAndOperators;
