import React from "react";
import { PageHeader, Card, DataTable } from "@salesforce/design-system-react";

import { DEDUP_SCENARIOS } from "../../../consts/dedup/componentNames";
import { ScenarioActions } from "./ScenarioPageHeaderActions";
//TODO: Change ScenariosRow.js file name to ScenariosColumn.jsx
import { ScenariosColumns } from "./ScenariosRow";
import useScenarios from "../../../hooks/dedup/useScenarios";

const Scenarios = () => {
  const [
    allScenarios,
    { onRowAction, createScenario, viewScenario, viewDuplicates }
  ] = useScenarios();

  return (
    <React.Fragment>
      <PageHeader
        className="slds-m-bottom_large"
        title="Scenarios"
        variant="object-home"
        onRenderActions={ScenarioActions.bind(null, createScenario)}
        trail={[<a href="javascript:void(0);">Dedup</a>]}
      />

      <Card style={{ flexGrow: 1 }} heading="All Scenarios">
        <DataTable
          items={allScenarios.data.data}
          noRowHover={true}
          viewScenario={viewScenario}
          viewDuplicates={viewDuplicates}
          onRowAction={onRowAction}
        >
          {ScenariosColumns}
        </DataTable>
      </Card>
    </React.Fragment>
  );
};

Scenarios.displayName = DEDUP_SCENARIOS;

export default Scenarios;
