import { useEffect } from "react";

import {
  CLOUD_STORAGE_PROVIDERS_AWS_S3,
  CLOUD_STORAGE_PROVIDERS_S3_COMPATIBLE,
  CLOUD_STORAGE_PROVIDERS_AZURE,
  CLOUD_STORAGE_PROVIDERS_GOOGLE_CLOUD_PLATFORM,
  //  CLOUD_STORAGE_PROVIDERS_MINIO
} from "../../consts/configurations/cloudStorageProviders";
import { useCloudStorageContext } from "../../contexts/cloudStorageContext";
import { useHandleButtonGroup } from "../useHandleForm";
import { QUICK_START_FORM_SET_PROVIDER } from "../../consts/configurations/configurationsActions";
import useSelectedProvider from "./useSelectedProvider";

const options = [
  { label: "AWS S3 Storage", value: CLOUD_STORAGE_PROVIDERS_AWS_S3 },
  { label: "S3 Compatible", value: CLOUD_STORAGE_PROVIDERS_S3_COMPATIBLE },
  { label: "Azure BLOB", value: CLOUD_STORAGE_PROVIDERS_AZURE },
  { label: "Google Cloud Platform", value: CLOUD_STORAGE_PROVIDERS_GOOGLE_CLOUD_PLATFORM },
];

const useCloudStorageProviders = () => {
  const [state, dispatch] = useCloudStorageContext();
  const handleButtonGroup = useHandleButtonGroup(QUICK_START_FORM_SET_PROVIDER, dispatch);
  const provider = useSelectedProvider(state);
  const selectedTab = state.provider;

  useEffect(() => {}, []);

  return [
    {
      selectedTab,
      provider,
      options,
    },
    { handleButtonGroup, dispatch },
  ];
};

export default useCloudStorageProviders;
