import React, { createContext, useReducer } from "react";

import { useStateContext, useDispatchContext } from "../hooks/configuration/useContextProvider";
import notificationReducer from "../reducers/notificationReducer";

const initialState = {
  isLoading: false,
  isOpen: false,
  variant: "success",
  duration: 2000,
  message: { heading: [] }
};

const stateContext = createContext();
const dispatchContext = createContext();

export const useInteractiveFetchStatusContext = () => [
  useStateContext(stateContext),
  useDispatchContext(dispatchContext)
];

export const InteractiveFetchStatusProvider = ({ children }) => {
  const [state, dispatch] = useReducer(notificationReducer, initialState);

  return (
    <stateContext.Provider value={state}>
      <dispatchContext.Provider value={dispatch}>{children}</dispatchContext.Provider>
    </stateContext.Provider>
  );
};
