import React, { useState } from "react";
import { Button, Checkbox, IconSettings } from "@salesforce/design-system-react";

//TODO: should name be plural or singular
const DuplicateRow = ({ tableFields, row, onViewRecord }) => {
  const [isChecked, setIsChecked] = useState(false);
  const getColumn = (field, row) => {
    return row[field];
  };

  return (
    <tr aria-level="1" className="slds-hint-parent">
      <td style={{ width: "3.25rem" }}>
        <div className="slds-checkbox">
          <Checkbox
            checked={isChecked}
            onChange={e => {
              setIsChecked(!isChecked);
              //onChange();
            }}
          />
        </div>
      </td>
      {tableFields.map(field => (
        <td>
          <div className="slds-truncate" title="Acme">
            {getColumn(field, row)}
          </div>
        </td>
      ))}

      <td className="slds-text-align_right">
        <IconSettings iconPath="/assets/icons">
          <Button
            align={"right"}
            iconCategory="utility"
            iconName="expand"
            iconVariant="border"
            iconSize="small"
            onClick={value => {
              // move record action to const file
              onViewRecord(row);
            }}
          />
        </IconSettings>
      </td>
    </tr>
  );
};

//TODO: set proptypes

export default DuplicateRow;
