import { useReducer, useEffect } from "react";

import { ENDPOINT_CONFIGURATION } from "../../consts/addresses";
import { CONFIGURATION_LOAD_INITIAL_STATE } from "../../consts/configurations/configurationsActions";
import { VALIDATION_BACKENDMESSAGE_INVALIDORGID } from "../../components/cloud/configuration/consts";
import configurationsReducer from "../../reducers/configurations/configurationsReducer";
import useFetch from "../useFetch";
import useCloudRoutes from "../useCloudRoutes";
import useInteractiveFetchStatus from "../useInteractiveFetchStatus";
import { isAnExistingConfiguration } from "../../components/cloud/configuration/utils";

export const mapBackendErrorMessage = (error) => {
  if (error === VALIDATION_BACKENDMESSAGE_INVALIDORGID) {
    return {
      heading: "Cannot find this SFDC Org ID in the configuration of the selected Heroku app.",
      details: `Please make sure the selected Heroku app does not already have a configuration tied to another SFDC Org.
              Each Heroku app can only have a single SFDC source org configured, and this cannot be changed once configured.`,
    };
  }

  return error;
};

export const useConfigurationRetrieve = () => {
  const [{ data, success, error }] = useFetch({ url: ENDPOINT_CONFIGURATION });
  const [state, dispatch] = useReducer(configurationsReducer, null);
  const [, { errorNotification }] = useInteractiveFetchStatus();
  const { goToSummary, goToStartProvisioning } = useCloudRoutes();

  useEffect(() => {
    if (success === true) {
      if (isAnExistingConfiguration(data)) {
        dispatch({ type: CONFIGURATION_LOAD_INITIAL_STATE, payload: data });
        goToSummary();
      } else {
        goToStartProvisioning();
      }
    }
  }, [data, success]);

  useEffect(() => {
    if (success === false) errorNotification(mapBackendErrorMessage(error), null);
  }, [error, success]);

  return [state, dispatch];
};

export const useSaveConfiguration = () => {
  const [saveStatus, setFetch] = useFetch();

  const saveConfiguration = (configuration) =>
    setFetch({ url: ENDPOINT_CONFIGURATION, method: "POST", data: configuration });

  return [saveStatus, saveConfiguration];
};
