import React from "react";
import PropTypes from "prop-types";

import { BASIC_CONFIG_SET_VALUE } from "../../../../../consts/configurations/configurationsActions";
import withProtectedExpandableSection from "./withProtectedExpandableSection";
import StatefulCheckbox from "../../../../commons/StatefulCheckbox";
import ProtectedInput from "./ProtectedInput";
import { useHandleCheckbox } from "../../../../../hooks/useHandleForm";
import { useCloudStorageContext } from "../../../../../contexts/cloudStorageContext";
import { ADVANCED_SECTION_TURN_OFF_STORAGE_IS_ENABLE } from "../../../../../consts/configurations/advancedSections";

const propTypes = {
  asyncIntervalMinutes: PropTypes.string.isRequired,
  asyncIntervalMinutesOnBlur: PropTypes.func.isRequired
};

const Other = ({ asyncIntervalMinutes, asyncIntervalMinutesOnBlur }) => {
  const [state, dispatch] = useCloudStorageContext();
  const { storageOff } = state;

  const handleCheckbox = useHandleCheckbox(BASIC_CONFIG_SET_VALUE, dispatch);

  return (
    <>
      <div className="slds-grid slds-gutters_small">
        <ProtectedInput
          label="Async Interval (minutes)"
          helpText="Determines the interval that the asynchronous heroku dyno runs at.
           Note that if you have set GRAX_ENV_ASYNC_INTERVAL_MINUTES in your heroku config vars,
            that value will override this field."
          variableName="asyncIntervalMinutes"
          value={asyncIntervalMinutes}
          required
          type="number"
          onBlur={asyncIntervalMinutesOnBlur}
        />
        <div className="slds-col slds-size_1-of-3">
          {ADVANCED_SECTION_TURN_OFF_STORAGE_IS_ENABLE === true && (
            <StatefulCheckbox
              label="Turn Off Storage"
              helpText="Do not turn off storage unless absolutely necessary, as this could cause extreme data loss"
              checked={storageOff}
              id="storageOff"
              onToggle={handleCheckbox}
            />
          )}
        </div>
      </div>
    </>
  );
};

Other.propTypes = propTypes;

export default withProtectedExpandableSection("Other")(Other);
