import { useEffect, useState } from "react";
import { GRAX_DATA_LAKE_PROXY_ADDRESS } from "../consts/addresses";
import useFetch from "./useFetch";

const useObjectsFetch = () => {
  const [sObjects, setSobjects] = useState([]);
  // TODO: fetch only salesforce standard and custom objects
  //  joelmsanto/dedup-ui-with-dedupscenariobackend
  const [_sObjects] = useFetch({
    url: `${GRAX_DATA_LAKE_PROXY_ADDRESS}/getDataTypes`,
    params: {
      sfdcorgid: ""
    }
  });

  useEffect(() => {
    if (_sObjects.data !== null) {
      //TODO: filter custom and standard objects
      setSobjects(
        _sObjects.data.filter(
          sObject => sObject.label.startsWith("grax") === false && sObject.label !== "logs"
        )
      );
    }
  }, [_sObjects.data]);

  return sObjects;
};

export default useObjectsFetch;
