import { useContext } from "react";

export const useStateContext = context => {
  const state = useContext(context);

  if (state === undefined) {
    throw new Error("useStateContext must be called inside provider");
  }

  return state;
};

export const useDispatchContext = context => {
  const dispatch = useContext(context);

  if (dispatch === undefined) {
    throw new Error("useDispatchContext must be called inside a provider");
  }

  return dispatch;
};
