import { useHistory } from "react-router-dom";

import {
  ROUTE_BASIC_CONFIGURATION,
  ROUTE_ADVANCED_CONFIGURATION,
  ROUTE_CONFIGURATION_SUMMARY,
  ROUTE_EMPTY_CONFIGURATION
} from "../consts/addresses";

const useCloudRoutes = () => {
  const history = useHistory();

  return {
    goToSummary: () => history.push(ROUTE_CONFIGURATION_SUMMARY),
    goToStartProvisioning: () => history.push(ROUTE_EMPTY_CONFIGURATION),
    goToBasicConfiguration: () => history.push(ROUTE_BASIC_CONFIGURATION),
    goToAdvancedConfiguration: () => history.push(ROUTE_ADVANCED_CONFIGURATION)
  };
};

export default useCloudRoutes;
