/* eslint-disable react/require-default-props */
import React, { useState, useEffect, useRef } from "react";
import PropTypes from "prop-types";

import { Checkbox, Tooltip, Modal, Button } from "@salesforce/design-system-react";

const fieldLevelHelpTextPropTypes = {
  label: PropTypes.string.isRequired,
  helpText: PropTypes.string,
};

const statefulCheckboxPropTypes = {
  id: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  checked: PropTypes.bool.isRequired,
  onToggle: PropTypes.func.isRequired,
  isDestructive: PropTypes.bool,
  helpText: PropTypes.string,
};

const destructiveWarningPropTypes = {
  isADestructiveAction: PropTypes.bool.isRequired,
};

const FieldLevelHelpText = ({ label, helpText }) => (
  <>
    <span className="slds-form-element__label">{label}</span>
    {helpText !== null && (
      <Tooltip
        content={helpText}
        align="top left"
        variant="learnMore"
        dialogClassName="dialog-classname"
      />
    )}
  </>
);

const DestructiveWarning = ({ isADestructiveAction = false }) => {
  const isMounted = useRef(false);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    if (isMounted.current === false) {
      isMounted.current = true;
    } else if (isADestructiveAction === false) {
      setIsOpen(true);
    }
  }, [isADestructiveAction]);

  return (
    <Modal
      noHeader
      footer={[<Button key="promptBtn" label="Ok" onClick={() => setIsOpen(false)} />]}
      isOpen={isOpen}
      onRequestClose={() => setIsOpen(false)}
      prompt="error"
      size="small"
      title="Confirmation Needed"
    >
      <div className="slds-m-around_medium">
        Once you save the configuration, you will lose the information for this feature you just
        disabled, and will need to re-enter the next time you enable the feature
      </div>
    </Modal>
  );
};

const StatefulCheckbox = ({
  id,
  label,
  checked,
  onToggle,
  helpText = null,
  isDestructive = false,
}) => {
  const [_checked, setIsChecked] = useState(false);

  useEffect(() => setIsChecked(checked), [checked]);

  return (
    <>
      <FieldLevelHelpText label={label} helpText={helpText} />
      <div className="slds-form-element__control">
        <div style={{ width: 0 }}>
          <Checkbox variant="toggle" id={id} checked={_checked} onChange={onToggle} />
        </div>
      </div>

      {isDestructive && <DestructiveWarning isADestructiveAction={_checked} />}
    </>
  );
};

FieldLevelHelpText.propTypes = fieldLevelHelpTextPropTypes;
DestructiveWarning.propTypes = destructiveWarningPropTypes;
StatefulCheckbox.propTypes = statefulCheckboxPropTypes;

export default StatefulCheckbox;
