import React, { Fragment } from "react";
import PropTypes from "prop-types";
import StatefulInput from "../../../../commons/StatefulInput";

const propTypes = {
  googleProjectId: PropTypes.string,
  googleBucketName: PropTypes.string,
  googlePrivateKey: PropTypes.string,
  googleClientEmail: PropTypes.string,
  onBlur: PropTypes.func.isRequired
};

const defaultProps = {
  googleProjectId: "",
  googleBucketName: "",
  googlePrivateKey: "",
  googleClientEmail: ""
};

const GoogleCloudPlatform = ({
  googleProjectId,
  googleBucketName,
  googlePrivateKey,
  googleClientEmail,
  onBlur
}) => (
  <Fragment>
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Google Project Id"
          required
          value={googleProjectId}
          id="googleProjectId"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Google Bucket Name"
          required
          value={googleBucketName}
          id="googleBucketName"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Google Private Key"
          secured
          required
          value={googlePrivateKey}
          id="googlePrivateKey"
          onBlur={onBlur}
        />
      </div>
    </div>
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Google Client Email"
          value={googleClientEmail}
          id="googleClientEmail"
          onBlur={onBlur}
        />
      </div>
    </div>
  </Fragment>
);

GoogleCloudPlatform.propTypes = propTypes;
GoogleCloudPlatform.defaultProps = defaultProps;

export default GoogleCloudPlatform;
