/* eslint-disable react/require-default-props */
import React, { Fragment } from "react";
import PropTypes from "prop-types";

import StatefulInput from "../../../../commons/StatefulInput";
import GRAXButtonGroup from "../../../../commons/ButtonGroup";

import {
  SERVER_SIDE_ENCRYPTIONS_AES256,
  SERVER_SIDE_ENCRYPTIONS_AWS_KMS,
  SERVER_SIDE_ENCRYPTIONS_NO_ENCRYPTIONS
} from "../../../../../consts/configurations/serverSideEncryptions";
import Subsection from "../../Subsection";

const options = [
  {
    label: "AES256",
    value: SERVER_SIDE_ENCRYPTIONS_AES256
  },
  {
    label: "AWS KMS",
    value: SERVER_SIDE_ENCRYPTIONS_AWS_KMS
  },
  {
    label: "No Encryption",
    value: SERVER_SIDE_ENCRYPTIONS_NO_ENCRYPTIONS
  }
];

const propTypes = {
  serverSideEncryption: PropTypes.oneOf([
    SERVER_SIDE_ENCRYPTIONS_AES256,
    SERVER_SIDE_ENCRYPTIONS_AWS_KMS,
    SERVER_SIDE_ENCRYPTIONS_NO_ENCRYPTIONS
  ]),
  serverSideEncryptionKmsKey: PropTypes.string,
  onBlur: PropTypes.func.isRequired,
  handleButtonGroup: PropTypes.func.isRequired
};

const ServerSideEncryption = ({
  serverSideEncryption = SERVER_SIDE_ENCRYPTIONS_AES256,
  serverSideEncryptionKmsKey,
  onBlur,
  handleButtonGroup
}) => (
  <Fragment>
    <Subsection heading="Server-Side Encryption" />
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <span className="slds-form-element__label">&nbsp;</span>
        <div className="slds-form-element__control">
          <GRAXButtonGroup
            options={options}
            value={serverSideEncryption}
            id="serverSideEncryption"
            onClick={handleButtonGroup}
          />
        </div>
      </div>
      {serverSideEncryption === SERVER_SIDE_ENCRYPTIONS_AWS_KMS && (
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="S3 KMS ID"
            value={serverSideEncryptionKmsKey}
            id="serverSideEncryptionKmsKey"
            onBlur={onBlur}
          />
        </div>
      )}
    </div>
  </Fragment>
);

ServerSideEncryption.propTypes = propTypes;

export default ServerSideEncryption;
