import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";

import { Input, InputIcon, Tooltip } from "@salesforce/design-system-react";

const propTypes = {
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  secured: PropTypes.bool,
  onBlur: PropTypes.func
};

const securedInput = (type, toggleSecurity) => ({
  iconRight: (
    <InputIcon
      name={type === "password" ? "preview" : "hide"}
      category="utility"
      onClick={toggleSecurity}
    />
  )
});

const blockedInput = (disabled, toggleDisability) => ({
  iconRight: (
    <InputIcon
      name={disabled === true ? "edit" : "error"}
      category="utility"
      onClick={toggleDisability}
    />
  )
});

const fieldLevelHelpTooltip = helpText => ({
  fieldLevelHelpTooltip: (
    <Tooltip id="field-level-help-tooltip" align="top left" content={helpText} />
  )
});

const StatefulInput = ({
  value,
  type = "text",
  helpText,
  disabled = false,
  onBlur,
  secured,
  blocked,
  ...props
}) => {
  const [_value, setValue] = useState(value);
  const [_type, setType] = useState(!secured ? type : "password");
  const [_disabled, setDisability] = useState(!blocked ? disabled : true);

  const toggleSecurity = () => setType(_type === "password" ? type : "password");
  const toggleDisability = () => setDisability(!_disabled);
  const _onBlur = e => {
    if (blocked === true) setDisability(true);
    if (onBlur !== undefined) onBlur(e);
  };

  useEffect(() => setValue(value), [value]);

  return (
    <Input
      value={_value}
      type={_type}
      disabled={_disabled}
      onBlur={_onBlur}
      onChange={e => setValue(e.target.value)}
      {...(secured === true ? securedInput(_type, toggleSecurity) : {})}
      {...(blocked === true ? blockedInput(_disabled, toggleDisability) : {})}
      {...(helpText !== undefined ? fieldLevelHelpTooltip(helpText) : {})}
      {...props}
    />
  );
};

StatefulInput.propTypes = propTypes;

export default StatefulInput;
