export const useFormDispatch = (actionType, dispatch) => {
  if (actionType === undefined) throw new Error("Action Name is required");
  if (dispatch === undefined) throw new Error("Dispatch is required");

  return (propertyName, value, meta) => {
    if (propertyName === undefined) throw new Error("Property name is required");

    dispatch({
      meta,
      type: actionType,
      payload: { [propertyName]: value }
    });
  };
};

export const useHandleInput = (actionType, dispatch) => {
  const onChange = useFormDispatch(actionType, dispatch);

  return (
    { target: { type, id, valueAsNumber, value } },
    { meta = {}, valueCasting = true } = {}
  ) => {
    if (id === undefined) throw new Error("Id is required");
    const castedValue = type === "number" && valueCasting === true ? valueAsNumber : value;

    onChange(id, castedValue, meta);
  };
};

export const useHandleCombobox = (actionType, dispatch) => {
  const onChange = useFormDispatch(actionType, dispatch);

  return (name, selection) => {
    if (name === undefined) throw new Error("Name is required");

    onChange(name, selection.length === 1 ? selection[0].value : null);
  };
};

export const useHandleCheckbox = (actionType, dispatch) => {
  const onChange = useFormDispatch(actionType, dispatch);

  return ({ target: { id } }, { checked }) => {
    if (id === undefined) throw new Error("Id is required");

    onChange(id, checked);
  };
};

export const useHandleButtonGroup = (actionType, dispatch) => {
  const onChange = useFormDispatch(actionType, dispatch);

  return (value, id, meta = {}) => {
    if (id === undefined) throw new Error("Id is required");

    onChange(id, value, meta);
  };
};
