import React from "react";
import PropTypes from "prop-types";

import _ from "lodash";

import { ScopedNotification, Icon } from "@salesforce/design-system-react";
import useVendorValidityMessage from "../../hooks/configuration/useVendorValidityMessage";

const propTypes = {
  validity: PropTypes.objectOf(PropTypes.string).isRequired,
};

const icon = (iconSettings) => (
  <Icon
    assistiveText={{
      label: iconSettings.label,
    }}
    category={iconSettings.category}
    colorVariant={iconSettings.colorVariant}
    name={iconSettings.name}
    size="x-small"
  />
);

const VendorValidityMessage = ({ variant, status, customMessage }) => {
  const { iconSettings, message } = useVendorValidityMessage(variant, status, customMessage);

  return (
    <>
      {iconSettings !== undefined && message !== undefined && (
        <div className="slds-grid slds-row slds-grid_align-center slds-m-bottom_x-small ">
          <ScopedNotification icon={icon(iconSettings)} theme="light">
            <p>{message}</p>
          </ScopedNotification>
        </div>
      )}
    </>
  );
};

VendorValidityMessage.propTypes = propTypes;

export default VendorValidityMessage;
