import {
  ADVANCED_CONFIG_SET_VALUE,
  ADVANCED_CONFIG_INITIAL_LOAD
} from "../../consts/configurations/configurationsActions";
import InvalidActionTypeError from "../../models/errors/InvalidActionTypeError";

const advancedConfigReducer = (state, action) => {
  const { payload, type } = action;

  switch (type) {
    case ADVANCED_CONFIG_INITIAL_LOAD:
    case ADVANCED_CONFIG_SET_VALUE:
      return { ...state, ...payload };
    default:
      throw new InvalidActionTypeError();
  }
};

export default advancedConfigReducer;
