import React, { useState } from "react";
import PropTypes from "prop-types";

import {
  QUICK_START_FORM_SET_PROVIDER,
  QUICK_START_FORM_SET_NAME,
} from "../../../../consts/configurations/configurationsActions";
import {
  SALESFORCE_LOGIN_SFDC,
  SALESFORCE_LOGIN_OAUTH,
  SALESFORCE_DATASOURCES_SOURCE,
  SALESFORCE_DATASOURCES_EINSTEIN,
  SALESFORCE_DATASOURCES_BULKRESTORE,
} from "./consts";

import { useSalesforceConnectionContext } from "./context";

import GRAXButtonGroup from "../../../commons/ButtonGroup";
import StatefulInput from "../../../commons/StatefulInput";
import ConfigurationNotice from "../../../commons/ConfigurationNotice";
import VendorValidityMessage from "../../../commons/VendorValidityMessage";
import UsernamePassword from "./UsernamePassword";
import OAuth from "./OAuth";

import { useAutoDismissToast } from "../../../../hooks/configuration/useIndividualVendorValidity";
import { useHandleButtonGroup } from "../../../../hooks/useHandleForm";
import useSelectedProvider from "../../../../hooks/configuration/useSelectedProvider";
import { useSFDCHandleInput } from "./hooks";

const propTypes = {
  // eslint-disable-next-line react/require-default-props
  showConnectionName: PropTypes.bool,
  variant: PropTypes.oneOf([
    SALESFORCE_DATASOURCES_SOURCE,
    SALESFORCE_DATASOURCES_EINSTEIN,
    SALESFORCE_DATASOURCES_BULKRESTORE,
  ]).isRequired,
};

const options = [
  {
    label: "Username / Password",
    value: SALESFORCE_LOGIN_SFDC,
  },
  {
    label: "OAuth",
    value: SALESFORCE_LOGIN_OAUTH,
    disabled: false,
  },
];

const SalesforceConnection = ({ variant, showConnectionName = false }) => {
  const [validation, setValidationStatus] = useState("");
  const [state, dispatch] = useSalesforceConnectionContext();
  const isOpen = useAutoDismissToast(validation);
  const onBlur = useSFDCHandleInput(variant, dispatch);
  const handleConnectionName = useSFDCHandleInput(variant, dispatch, QUICK_START_FORM_SET_NAME);
  const onBlurButtonGroup = useHandleButtonGroup(QUICK_START_FORM_SET_PROVIDER, dispatch);
  const handleButtonGroup = (selected, id) => onBlurButtonGroup(selected, id, { variant });

  const dataSourceVariant = state[variant];
  const selectedTab = dataSourceVariant.provider;
  const dataSource = useSelectedProvider(dataSourceVariant);

  return (
    <>
      {variant === SALESFORCE_DATASOURCES_SOURCE && (
        <ConfigurationNotice
          text={`Please enter credentials for the integration user the GRAX app will use to log in. In order
        to prevent crossing your wires, the credentials entered here must be for this current SFDC
        org you are in.`}
        />
      )}

      {isOpen === true && (
        <VendorValidityMessage
          variant={variant}
          status={validation.status}
          customMessage={validation.customMessage}
        />
      )}
      {showConnectionName === true && (
        <div className="slds-grid slds-gutters_small slds-m-bottom_small">
          <div className="slds-col slds-size_2-of-3">
            <StatefulInput
              label="Connection Name"
              required
              value={dataSource.name}
              id="name"
              helpText='Please name your Salesforce connection e.g. "Dev Sandbox'
              onBlur={handleConnectionName}
            />
          </div>
        </div>
      )}
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_2-of-3">
          <GRAXButtonGroup
            options={options}
            value={selectedTab}
            id="provider"
            onClick={handleButtonGroup}
          />
        </div>
      </div>
      {selectedTab === SALESFORCE_LOGIN_SFDC ? (
        <UsernamePassword
          variant={variant}
          userName={dataSource.userName}
          password={dataSource.password}
          token={dataSource.token}
          url={dataSource.url}
          name={dataSource.name}
          organizationId={dataSource.organizationId}
          onBlur={onBlur}
          onValidated={setValidationStatus}
        />
      ) : (
        <OAuth
          dataSource={dataSource}
          variant={variant}
          dispatch={dispatch}
          onValidated={setValidationStatus}
        />
      )}
    </>
  );
};

SalesforceConnection.propTypes = propTypes;

export default SalesforceConnection;
