import { useEffect } from "react";

import {
  NOTIFICATION_IS_CLOSE,
  NOTIFICATION_SUCCESS,
  NOTIFICATION_ERROR,
  SPINNER_IS_STOPPED,
  SPINNER_IS_RUNNING
} from "../consts/interactiveFetchStatusActions";
import { useInteractiveFetchStatusContext } from "../contexts/interactiveFetchStatusContext";

const useInteractiveFetchStatus = () => {
  const [state, dispatch] = useInteractiveFetchStatusContext();

  const spinnerIsRunning = () =>
    dispatch({
      type: SPINNER_IS_RUNNING
    });

  const spinnerIsStopped = () =>
    dispatch({
      type: SPINNER_IS_STOPPED
    });

  const successNotification = message =>
    dispatch({
      type: NOTIFICATION_SUCCESS,
      payload: { message }
    });

  const errorNotification = (message, duration = 5000) =>
    dispatch({
      type: NOTIFICATION_ERROR,
      payload: { message, duration }
    });

  const hideNotification = () =>
    dispatch({
      type: NOTIFICATION_IS_CLOSE
    });

  return [
    state,
    { successNotification, errorNotification, hideNotification, spinnerIsRunning, spinnerIsStopped }
  ];
};

export const useToggleSpinner = isLoading => {
  const [, { spinnerIsRunning, spinnerIsStopped }] = useInteractiveFetchStatus();
  useEffect(() => (isLoading === true ? spinnerIsRunning : spinnerIsStopped)(), [isLoading]);
};

export default useInteractiveFetchStatus;
