import React, { useState, Fragment } from "react";
import PropTypes from "prop-types";

import { ExpandableSection } from "@salesforce/design-system-react";

import MatchCriterias from "./MatchCriterias";
import FilterCriterias from "./FilterCriterias";
import { useScenarioStore } from "../../../contexts/scenarioContext";
import GRAXCombobox from "../../commons/GRAXCombobox";
import TreeModal from "./metadata/TreeModal";

const propTypes = {
  matchCriteria: PropTypes.shape({
    field: PropTypes.string,
    operator: PropTypes.string,
    value: PropTypes.string,
    stopword: PropTypes.array,
    synonyms: PropTypes.array,
    cleanse: PropTypes.bool
  }),
  filterCriteria: PropTypes.shape({
    field: PropTypes.string,
    operator: PropTypes.string,
    value: PropTypes.any
  }),
  operators: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string
    })
  ),
  stopwords: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string
    })
  ),
  synonyms: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string
    })
  ),
  onChangeMatchCriteria: PropTypes.func,
  onChangeFilterCriteria: PropTypes.func
};

const ScenarioCriterias = ({
  matchCriteria,
  filterCriteria,
  objectName,
  fields,
  docPrefixedFields,
  sObjects,
  onChangeObject,
  onChangeFields,
  onChangeMatchCriteria,
  onChangeFilterCriteria
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [criteria, setCriteria] = useState({});
  const scenarioStore = useScenarioStore();
  const { operators } = scenarioStore.metadata;

  const setMetadata = criteria => {
    setIsOpen(true);
    setCriteria(criteria);
  };

  return (
    <Fragment>
      <ExpandableSection title="Objects" className="slds-m-bottom_medium" nonCollapsible>
        <div className="slds-grid slds-gutters_small">
          <div className="slds-col slds-size_1-of-5">
            <GRAXCombobox
              options={sObjects}
              labels={{ placeholder: "Select option" }}
              value={objectName}
              onChange={(event, selection) => {
                //TODO: improve object name and object index retrievement
                const sObject =
                  selection.length === 1 ? selection[0] : { objectName: "", objectIndex: "" };
                onChangeObject(sObject.label, sObject.id);
              }}
            />
          </div>
        </div>
      </ExpandableSection>

      <ExpandableSection title="Matching Criterias" className="slds-m-bottom_medium" nonCollapsible>
        <MatchCriterias
          rows={matchCriteria}
          docPrefixedFields={docPrefixedFields}
          operators={operators}
          rowsNum={matchCriteria.length}
          onChange={onChangeMatchCriteria}
          setMetadata={setMetadata}
        />
      </ExpandableSection>
      <ExpandableSection title="Filter Criterias" className="slds-m-bottom_medium" nonCollapsible>
        <FilterCriterias
          rows={filterCriteria}
          docPrefixedFields={docPrefixedFields}
          operators={operators}
          onChange={onChangeFilterCriteria}
        />
      </ExpandableSection>
      <TreeModal isOpen={isOpen} criteria={criteria} onRequestClose={() => setIsOpen(false)} />
    </Fragment>
  );
};

ScenarioCriterias.propType = propTypes;
export default ScenarioCriterias;
