import React from "react";

import PropTypes from "prop-types";

import withProtectedExpandableSection from "./withProtectedExpandableSection";
import ProtectedInput from "./ProtectedInput";
import GRAXButtonGroup from "../../../../commons/ButtonGroup";
import {
  PUSH_TO_QUEUE_PROVIDER_APIGEE,
  PUSH_TO_QUEUE_PROVIDER_KAFKA
} from "../../../../../consts/configurations/pushToQueueProvider";

const options = [
  {
    label: "Apigee",
    value: PUSH_TO_QUEUE_PROVIDER_APIGEE
  },
  {
    label: "Kafka",
    value: PUSH_TO_QUEUE_PROVIDER_KAFKA
  }
];

const propTypes = {
  provider: PropTypes.string.isRequired,
  apiEndpoint: PropTypes.string.isRequired,
  apiClientId: PropTypes.string.isRequired,
  apiClientSecret: PropTypes.string.isRequired,
  authorizationToken: PropTypes.string.isRequired,
  onToggle: PropTypes.func.isRequired,
  onBlur: PropTypes.func.isRequired
};

const PushtoQueue = ({
  provider,
  apiEndpoint,
  apiClientId,
  apiClientSecret,
  authorizationToken,
  onToggle,
  onBlur
}) => {
  const inputs = [];

  if (provider === PUSH_TO_QUEUE_PROVIDER_APIGEE || provider === PUSH_TO_QUEUE_PROVIDER_KAFKA) {
    inputs.push(
      <ProtectedInput
        label="Push-to-Queue API Endpoint"
        variableName="apiEndpoint"
        value={apiEndpoint}
        required
        onBlur={onBlur}
      />
    );

    switch (provider) {
      case PUSH_TO_QUEUE_PROVIDER_APIGEE:
        inputs.push([
          <ProtectedInput
            label="Push-to-Queue API Client ID"
            variableName="apiClientId"
            value={apiClientId}
            required
            onBlur={onBlur}
          />,
          <ProtectedInput
            label="Push-to-Queue API Client Secret"
            variableName="apiClientSecret"
            value={apiClientSecret}
            required
            onBlur={onBlur}
          />
        ]);
        break;
      case PUSH_TO_QUEUE_PROVIDER_KAFKA:
        inputs.push(
          <ProtectedInput
            label="Push-to-Queue Authorization Token"
            variableName="authorizationToken"
            value={authorizationToken}
            required
            onBlur={onBlur}
          />
        );
        break;
      default:
    }
  }

  return (
    <>
      <div className="slds-grid slds-gutters_small slds-m-top_x-small">
        <div className="slds-col slds-size_1-of-3">
          <GRAXButtonGroup
            options={options}
            value={provider}
            required={false}
            onClick={selected => onToggle("provider", selected)}
          />
        </div>
      </div>
      <div className="slds-grid slds-gutters_small">{inputs}</div>
    </>
  );
};

PushtoQueue.propTypes = propTypes;

export default withProtectedExpandableSection("Push to Queue")(PushtoQueue);
