import { createContext } from "react";

import { useStateContext, useDispatchContext } from "../hooks/configuration/useContextProvider";
import { withAdvancedConfigProvider } from "./contextProviders";
import { SALESFORCE_GIT_INTEGRATION_GITHUB } from "../consts/configurations/sfdcGitIntegration";

const stateContext = createContext();
const dispatchContext = createContext();

export const useMetadataBackupContext = () => [
  useStateContext(stateContext),
  useDispatchContext(dispatchContext)
];
const initialState = {
  isEnabled: false,
  sourceControlEnabled: false,
  sourceControlProvider: SALESFORCE_GIT_INTEGRATION_GITHUB
};

export const MetadataBackupProvider = withAdvancedConfigProvider(
  stateContext,
  dispatchContext,
  initialState
);
