import React from "react";
import PropTypes from "prop-types";

import Combobox from "./Combobox";
import useAwsRegions from "../../hooks/configuration/useAwsRegions";
import ALL_REGIONS from "../../consts/regions";

const propTypes = {
  label: PropTypes.string.isRequired,
  value: PropTypes.oneOf([...ALL_REGIONS, ""]).isRequired,
  // eslint-disable-next-line react/require-default-props
  id: PropTypes.string,
  onChange: PropTypes.func.isRequired
};

const AwsRegions = ({ label, value, id, onChange }) => {
  const [options, getRegionLabel] = useAwsRegions();

  return (
    <Combobox
      value={value}
      options={options}
      id={id}
      selection={getRegionLabel(value)}
      onSelect={(_event, selection) => onChange(selection)}
      required
      labels={{
        label,
        placeholder: "Select Option"
      }}
    />
  );
};

AwsRegions.propTypes = propTypes;

export default AwsRegions;
