import React from "react";
import PropTypes from "prop-types";
import AwsRegions from "../../../../commons/AwsRegions";
import StatefulInput from "../../../../commons/StatefulInput";
import ALL_REGIONS from "../../../../../consts/regions";

const propTypes = {
  s3CompatibleEndpoint: PropTypes.string,
  bucket: PropTypes.string,
  accessKeyName: PropTypes.string,
  secretAccessKey: PropTypes.string,
  region: PropTypes.oneOf([...ALL_REGIONS, ""]),
  onBlur: PropTypes.func.isRequired,
  handleCheckbox: PropTypes.func.isRequired,
  handleCombobox: PropTypes.func.isRequired,
};

const defaultProps = {
  s3CompatibleEndpoint: "",
  bucket: "",
  accessKeyName: "",
  secretAccessKey: "",
  region: "",
};

const S3Compatible = ({
  s3CompatibleEndpoint,
  bucket,
  accessKeyName,
  secretAccessKey,
  region,
  onBlur,
  handleCombobox,
}) => (
  <>
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Endpoint"
          required
          value={s3CompatibleEndpoint}
          id="s3CompatibleEndpoint"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput label="S3 Bucket Name" required value={bucket} id="bucket" onBlur={onBlur} />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <AwsRegions
          label="S3 Region"
          value={region}
          onChange={(selections) => handleCombobox("region", selections)}
        />
      </div>
    </div>
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Access Key ID"
          required
          secured
          value={accessKeyName}
          id="accessKey"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Secret Access Key"
          required
          secured
          value={secretAccessKey}
          id="secretAccessKey"
          onBlur={onBlur}
        />
      </div>
    </div>
  </>
);

S3Compatible.propTypes = propTypes;
S3Compatible.defaultProps = defaultProps;

export default S3Compatible;
