import React, { memo, useRef } from "react";
import PropType from "prop-types";

import { ExpandableSection, Input, Checkbox } from "@salesforce/design-system-react";

import { DEDUP_SCENARIO_GENERIAL } from "../../../consts/dedup/componentNames";

const propTypes = {
  name: PropType.string.required,
  description: PropType.string.required,
  isActive: PropType.bool.required,
  onChangeName: PropType.func,
  onChangeDescription: PropType.func,
  onChangeNameIsActive: PropType.func
};

const ScenarioGeneral = memo(
  ({ name, description, isActive, onChangeName, onChangeDescription, onChangeIsActive }) => {
    const c = useRef(0);
    c.current = c.current + 1;
    // console.log(c, "general");
    return (
      <ExpandableSection
        title="General"
        className="slds-m-bottom_medium slds-m-top_none"
        nonCollapsible
      >
        <div className="slds-grid slds-gutters_small">
          <div className="slds-col slds-size_2-of-5">
            <Input
              label="Name"
              required
              value={name}
              onChange={e => onChangeName(e.target.value)}
            />
          </div>
          <div className="slds-col slds-size_2-of-5">
            <Input
              label="Description"
              value={description}
              onChange={e => onChangeDescription(e.target.value)}
            />
          </div>
          <div className="slds-col slds-size_1-of-5">
            <div className="slds-form-element">
              <label className="slds-form-element__label">Is Active</label>
              <Checkbox
                checked={isActive}
                variant="toggle"
                onChange={(_event, { checked }) => onChangeIsActive(checked)}
              />
            </div>
          </div>
        </div>
      </ExpandableSection>
    );
  }
);

ScenarioGeneral.prototype = propTypes;
ScenarioGeneral.prototype.displayName = DEDUP_SCENARIO_GENERIAL;

export default ScenarioGeneral;
