import { useContext, useEffect } from "react";
import { useHistory } from "react-router-dom";

import {
  SCENARIO_FETCH_ENUMS_RUN,
  SCENARIO_FETCH_ENUMS_DUPLICATE,
  SCENARIO_FETCH_ENUMS_UPDATE,
  SCENARIO_FETCH_ENUMS_DELETE,
  SCENARIO_FETCH_ENUMS_RESULTS
} from "../../consts/dedup/scenarioFetchEnums";
import {
  useAllScenariosFetch,
  useDeleteScenario,
  useCloneScenario,
  useRunDeduplication
} from "./useScenarioFetch";
import { DEDUP_PATH } from "../../consts/addresses";
import { ToastContext } from "../../contexts/toastContext";

const useScenarios = () => {
  const [allScenarios, fetchAllScenarios] = useAllScenariosFetch();
  const [isDeleted, deleteScenario] = useDeleteScenario();
  const [isRunning, runDeduplication] = useRunDeduplication();
  const [isCloned, cloneScenario] = useCloneScenario();
  const history = useHistory();
  const { openToast } = useContext(ToastContext);

  useEffect(fetchAllScenarios, []);

  useEffect(() => {
    if (isCloned.data !== null && isCloned.data.data.id)
      openToast({
        heading: ["Scenario Cloned"]
      });
    else if (isDeleted.status !== null && isDeleted.status === 200) {
      openToast({
        heading: ["Scenario Deleted"]
      });
      fetchAllScenarios();
    } else if (isRunning.data !== null && isRunning.data.status === "success") {
      openToast({
        heading: ["Scenario is Running"]
      });
      fetchAllScenarios();
    }
  }, [isCloned.data, isDeleted.data, isRunning.data]);

  const createScenario = () => history.push(`${DEDUP_PATH}/scenarios/new`);
  const viewScenario = scenarioId => history.push(`${DEDUP_PATH}/scenarios/${scenarioId}`);
  //TODO: set propert route name /grax-app/client/duplicates/:id or /grax-app/client/results/:id ?
  const viewDuplicates = scenarioId => history.push(`${DEDUP_PATH}/duplicates/${scenarioId}`);

  const onRowAction = (scenarioId, selected) => {
    switch (selected.value) {
      case SCENARIO_FETCH_ENUMS_RUN:
        //TODO: change name to run scenario
        runDeduplication(scenarioId);
        break;
      //TODO: change name to view duplicates
      case SCENARIO_FETCH_ENUMS_RESULTS:
        //TODO retrieve scenario id
        viewDuplicates(scenarioId);
        break;
      case SCENARIO_FETCH_ENUMS_DUPLICATE:
        cloneScenario(scenarioId);
        break;
      case SCENARIO_FETCH_ENUMS_UPDATE:
        viewScenario(scenarioId);
        break;
      case SCENARIO_FETCH_ENUMS_DELETE:
        deleteScenario(scenarioId);
        break;
      default:
    }
  };

  return [
    allScenarios,
    {
      createScenario,
      viewScenario,
      viewDuplicates,
      onRowAction
    }
  ];
};

export default useScenarios;
