import React, { Fragment } from "react";
import PropTypes from "prop-types";

import StatefulInput from "../../../../commons/StatefulInput";

const propTypes = {
  user: PropTypes.string,
  password: PropTypes.string,
  url: PropTypes.string,
  port: PropTypes.number,
  onBlur: PropTypes.func.isRequired
};

const defaultProps = {
  user: "",
  password: "",
  url: "",
  port: undefined
};

const ElasticCo = ({ user, password, url, port, onBlur }) => {
  return (
    <Fragment>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput label="Elastic Username" required value={user} id="user" onBlur={onBlur} />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="Elastic Password"
            required
            secured
            value={password}
            id="password"
            onBlur={onBlur}
          />
        </div>
      </div>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput label="Elastic URL" required value={url} id="url" onBlur={onBlur} />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            type="number"
            label="Elastic Port"
            required
            value={port}
            id="port"
            onBlur={onBlur}
          />
        </div>
      </div>
    </Fragment>
  );
};

ElasticCo.propTypes = propTypes;
ElasticCo.defaultProps = defaultProps;

export default ElasticCo;
