import React from "react";
import {
  Button,
  PageHeader,
  PageHeaderControl,
  Icon,
  Card,
  ScopedNotification
} from "@salesforce/design-system-react";

import BasicConfigurationSummary from "./BasicConfigurationSummary";
import { useConfigurationsContext } from "../../../../contexts/configurationsContext";
import useCloudRoutes from "../../../../hooks/useCloudRoutes";

const ConfigurationSummary = () => {
  const [configuration] = useConfigurationsContext();
  const { goToBasicConfiguration, goToAdvancedConfiguration } = useCloudRoutes();

  const actionsBasic = () => (
    <PageHeaderControl>
      <Button label="Update Credentials" onClick={() => goToBasicConfiguration()} />
    </PageHeaderControl>
  );

  const actionsAdvanced = () => (
    <PageHeaderControl>
      <Button label="Edit Advanced Configuration" onClick={() => goToAdvancedConfiguration()} />
    </PageHeaderControl>
  );

  const iconBasic = (
    <Icon
      assistiveText={{ label: "Required Configuration" }}
      category="standard"
      name="calibration"
    />
  );

  const iconAdvanced = (
    <Icon assistiveText={{ label: "Advanced Configuration" }} category="standard" name="custom" />
  );

  return (
    <div className="summaryCard">
      <Card hasNoHeader>
        <PageHeader
          onRenderActions={actionsBasic}
          title="Required Configuration"
          variant="record-home"
          icon={iconBasic}
        />
        <BasicConfigurationSummary configuration={configuration} />
      </Card>
      <Card hasNoHeader>
        <PageHeader
          onRenderActions={actionsAdvanced}
          title="Advanced Configuration"
          variant="record-home"
          icon={iconAdvanced}
        />
        <Card hasNoHeader>
          <ScopedNotification theme="dark">
            <p>Only advanced users should edit these settings.</p>
          </ScopedNotification>
        </Card>
      </Card>
    </div>
  );
};

export default ConfigurationSummary;
