import InvalidActionTypeError from "../models/errors/InvalidActionTypeError";

import {
  NOTIFICATION_IS_OPEN,
  NOTIFICATION_IS_CLOSE,
  NOTIFICATION_SUCCESS,
  NOTIFICATION_ERROR,
  SPINNER_IS_RUNNING,
  SPINNER_IS_STOPPED
} from "../consts/interactiveFetchStatusActions";

const setHeadings = message => {
  if (typeof message === "string") {
    return {
      heading: [message]
    };
  }

  return message;
};
const notificationReducer = (state, { type, payload }) => {
  switch (type) {
    case SPINNER_IS_RUNNING:
      return { ...state, isLoading: true };
    case SPINNER_IS_STOPPED:
      return { ...state, isLoading: false };
    case NOTIFICATION_IS_OPEN:
      return { ...state, isOpen: true };
    case NOTIFICATION_IS_CLOSE:
      return { ...state, isOpen: false };
    case NOTIFICATION_SUCCESS:
      return { ...state, isOpen: true, variant: "success", message: setHeadings(payload.message) };
    case NOTIFICATION_ERROR: {
      return {
        ...state,
        isOpen: true,
        variant: "error",
        duration: payload.duration,
        message: setHeadings(payload.message)
      };
    }

    default:
      throw new InvalidActionTypeError();
  }
};

export default notificationReducer;
