import { createContext } from "react";

import { withQuickStartProvider } from "./contextProviders";
import { useStateContext, useDispatchContext } from "../hooks/configuration/useContextProvider";

import {
  ELASTICSEARCH_PROVIDERS_BONSAI,
  ELASTICSEARCH_PROVIDERS_AWS,
  ELASTICSEARCH_PROVIDERS_ELASTIC_CO,
} from "../consts/configurations/elasticsearchProviders";

const initialState = {
  name: "Default",
  provider: ELASTICSEARCH_PROVIDERS_BONSAI,
  [ELASTICSEARCH_PROVIDERS_BONSAI]: {},
  [ELASTICSEARCH_PROVIDERS_AWS]: {},
  [ELASTICSEARCH_PROVIDERS_ELASTIC_CO]: {},
};

const stateContext = createContext();
const dispatchContext = createContext();

export const useElasticsearchContext = () => [
  useStateContext(stateContext),
  useDispatchContext(dispatchContext),
];

export const ElasticsearchProvider = withQuickStartProvider(
  stateContext,
  dispatchContext,
  initialState
);
