import React, { Fragment } from "react";
import PropTypes from "prop-types";

import { Route } from "react-router-dom";

import {
  ROUTE_CONFIGURATION,
  ROUTE_BASIC_CONFIGURATION,
  ROUTE_CLOUD_CLIENT,
  ROUTE_ADVANCED_CONFIGURATION,
  ROUTE_EMPTY_CONFIGURATION,
  DEDUP_SCENARIOS_PATH,
  DEDUP_SCENARIOS_DETAILS_PATH,
  DEDUP_SCENARIOS_NEW_PATH,
  DEDUP_DUPLICATES_DETAILS_PATH,
  ROUTE_NEW_CONFIGURATION
} from "../../consts/addresses";

export const NestedRoutesPropTypes = {
  routes: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.string.isRequired,
      path: PropTypes.oneOf([
        ROUTE_CONFIGURATION,
        ROUTE_BASIC_CONFIGURATION,
        ROUTE_CLOUD_CLIENT,
        ROUTE_ADVANCED_CONFIGURATION,
        ROUTE_EMPTY_CONFIGURATION,
        DEDUP_SCENARIOS_PATH,
        DEDUP_SCENARIOS_DETAILS_PATH,
        DEDUP_SCENARIOS_NEW_PATH,
        DEDUP_DUPLICATES_DETAILS_PATH
      ]).isRequired,
      exact: PropTypes.bool,
      component: PropTypes.elementType.isRequired
    })
  ).isRequired
};

const NestedRoutes = ({ routes = [] }) => {
  return (
    <Fragment>
      {routes.map(route => (
        <Route
          key={route.key}
          path={route.path}
          exact={route.exact}
          render={() => <route.component routes={route.routes} />}
        />
      ))}
    </Fragment>
  );
};

export const initialEntries = appName => {
  const entries = [];

  if (appName === "configuration") {
    entries.push(ROUTE_CONFIGURATION);
  } else if (appName === "cloud") {
    entries.push(ROUTE_NEW_CONFIGURATION);
  } else {
    entries.push("/");
  }

  return entries;
};

NestedRoutes.propTypes = NestedRoutesPropTypes;

export default NestedRoutes;
