import React from "react";
import PropTypes from "prop-types";
import { ScopedNotification } from "@salesforce/design-system-react";

const propTypes = {
  text: PropTypes.string.isRequired
};

const ConfigurationNotice = ({ text }) => (
  <ScopedNotification>
    <p>{text}</p>
  </ScopedNotification>
);

ConfigurationNotice.propTypes = propTypes;

export default ConfigurationNotice;
