import React from "react";
import PropTypes from "prop-types";

import { ButtonGroup, Button } from "@salesforce/design-system-react";

const propTypes = {
  options: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string.isRequired,
      value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      disabled: PropTypes.bool
    })
  ).isRequired,
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  onClick: PropTypes.func.isRequired,
  id: PropTypes.string,
  required: PropTypes.bool
};

const defaultProps = { required: true, id: null };

const GRAXButtonGroup = ({ options, value, id, required = true, onClick }) => {
  const _onClick = selected => {
    if (required === false && selected === value) {
      onClick(null);
    } else {
      onClick(selected, id);
    }
  };

  return (
    <ButtonGroup>
      {options.map(option => (
        <Button
          label={option.label}
          key={option.label}
          onClick={() => _onClick(option.value)}
          variant={option.value === value ? "brand" : "neutral"}
          disabled={option.disabled}
        />
      ))}
    </ButtonGroup>
  );
};

GRAXButtonGroup.propTypes = propTypes;
GRAXButtonGroup.defaultProps = defaultProps;

export default GRAXButtonGroup;
