/* eslint-disable react/require-default-props */
import React, { Fragment, useEffect } from "react";
import PropTypes from "prop-types";

import { Button } from "@salesforce/design-system-react";

import {
  SALESFORCE_DATASOURCES_SOURCE,
  SALESFORCE_DATASOURCES_EINSTEIN,
  SALESFORCE_DATASOURCES_BULKRESTORE,
} from "./consts";

import StatefulInput from "../../../commons/StatefulInput";

import useIndividualVendorValidity from "../../../../hooks/configuration/useIndividualVendorValidity";
import URLStateFulInput from "./URLStateFulInput";

const propTypes = {
  variant: PropTypes.oneOf([
    SALESFORCE_DATASOURCES_SOURCE,
    SALESFORCE_DATASOURCES_EINSTEIN,
    SALESFORCE_DATASOURCES_BULKRESTORE,
  ]).isRequired,
  userName: PropTypes.string,
  password: PropTypes.string,
  token: PropTypes.string,
  url: PropTypes.string,
  name: PropTypes.string,
  organizationId: PropTypes.string,
  onBlur: PropTypes.func,
  onValidated: PropTypes.func,
};

const UsernamePassword = ({
  variant,
  name,
  userName,
  password,
  token,
  url,
  organizationId,
  onBlur,
  onValidated,
}) => {
  const [validation, validateVendor] = useIndividualVendorValidity();

  useEffect(() => onValidated(validation), [validation]);

  const onValidation = () =>
    validateVendor({
      variant,
      type: "salesforce",
      credentials: {
        provider: "salesforce",
        userName,
        password,
        token,
        url,
        name,
        organizationId,
      },
    });

  return (
    <Fragment>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="SFDC Username"
            required
            value={userName}
            id="userName"
            onBlur={onBlur}
          />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="SFDC Password"
            required
            secured
            value={password}
            id="password"
            onBlur={onBlur}
          />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="SFDC Security Token"
            required
            secured
            value={token}
            id="token"
            onBlur={onBlur}
          />
        </div>
      </div>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <URLStateFulInput url={url} onBlur={onBlur} />
        </div>
        {organizationId && (
          <div className="slds-col slds-size_1-of-3">
            <StatefulInput
              readOnly
              label="SFDC Organization ID (18 Digit)"
              value={organizationId}
              id="organizationId"
              onBlur={onBlur}
            />
          </div>
        )}
      </div>
      <div className="slds-grid slds-gutters_small slds-m-top_small">
        <div className="slds-col slds-size_1-of-3">
          <Button label="Validate SFDC Connection" onClick={onValidation} />
        </div>
      </div>
    </Fragment>
  );
};

UsernamePassword.propTypes = propTypes;

export default UsernamePassword;
