import { createContext } from "react";

import { withQuickStartProvider } from "../../../../contexts/contextProviders";
import {
  useStateContext,
  useDispatchContext,
} from "../../../../hooks/configuration/useContextProvider";

import {
  SALESFORCE_DATASOURCES_SOURCE,
  SALESFORCE_DATASOURCES_EINSTEIN,
  SALESFORCE_DATASOURCES_BULKRESTORE,
} from "./consts";

import salesforceConnectionReducer from "./reducer";
import { setDataSourceInitialState } from "./utils";

export const initialState = {
  ...setDataSourceInitialState(SALESFORCE_DATASOURCES_SOURCE),
  ...setDataSourceInitialState(SALESFORCE_DATASOURCES_EINSTEIN),
  ...setDataSourceInitialState(SALESFORCE_DATASOURCES_BULKRESTORE),
};

const stateContext = createContext();
const dispatchContext = createContext();

export const useSalesforceConnectionContext = () => [
  useStateContext(stateContext),
  useDispatchContext(dispatchContext),
];

export const SalesforceConnectionProvider = withQuickStartProvider(
  stateContext,
  dispatchContext,
  initialState,
  salesforceConnectionReducer
);
