import React from "react";
import PropTypes from "prop-types";

import { ExpandableSection } from "@salesforce/design-system-react";
import StatefulInput from "../../../../commons/StatefulInput";
import { useHandleInput, useHandleCheckbox } from "../../../../../hooks/useHandleForm";
import { ADVANCED_CONFIG_SET_VALUE } from "../../../../../consts/configurations/configurationsActions";
import { useOAuthContext } from "../../../../../contexts/oauthContext";
import StatefulCheckbox from "../../../../commons/StatefulCheckbox";

const propTypes = {
  clientId: PropTypes.string,
  tokenUrl: PropTypes.string,
  clientSecret: PropTypes.string,
};

const defaultProps = {
  clientId: "",
  tokenUrl: "",
  clientSecret: "",
};

const EnableOAuth = () => {
  const [{ enableOAuth, clientId, tokenUrl, clientSecret }, dispatch] = useOAuthContext();
  const handleCheckbox = useHandleCheckbox(ADVANCED_CONFIG_SET_VALUE, dispatch);
  const onBlur = useHandleInput(ADVANCED_CONFIG_SET_VALUE, dispatch);

  return (
    <ExpandableSection title="GRAX OAuth" className="slds-m-bottom_medium slds-m-top_none">
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulCheckbox
            label="Enable OAuth"
            helpText="Enable to configure OAuth on the data lake side (e.g. gateway setups).
            If you want to enable Salesforce OAuth, that must be done in the above Salesforce section"
            checked={enableOAuth}
            id="enableOAuth"
            variant="toggle"
            onToggle={handleCheckbox}
          />
        </div>
      </div>
      {enableOAuth === true && (
        <div className="slds-grid slds-gutters_small">
          <div className="slds-col slds-size_1-of-3">
            <StatefulInput
              label="OAuth Client ID"
              value={clientId}
              id="clientId"
              onBlur={onBlur}
              required={true}
            />
          </div>
          <div className="slds-col slds-size_1-of-3">
            <StatefulInput
              label="OAuth Client Secret"
              value={clientSecret}
              id="clientSecret"
              onBlur={onBlur}
              required={true}
            />
          </div>
          <div className="slds-col slds-size_1-of-3">
            <StatefulInput
              label="OAuth Token URL"
              value={tokenUrl}
              id="tokenUrl"
              onBlur={onBlur}
              required={true}
            />
          </div>
        </div>
      )}
    </ExpandableSection>
  );
};

EnableOAuth.propTypes = propTypes;
EnableOAuth.defaultProps = defaultProps;

export default EnableOAuth;
