import React from "react";
import PropTypes from "prop-types";

import { Modal, Button } from "@salesforce/design-system-react";

const propTypes = {
  isOpen: PropTypes.bool.isRequired,
  onRequestClose: PropTypes.func.isRequired,
  confirm: PropTypes.func.isRequired
};

const RevealSectionWarning = ({ isOpen, onRequestClose, confirm }) => {
  return (
    <Modal
      noHeader
      footer={[
        <Button key="promptBtn" label="Cancel" onClick={onRequestClose} />,
        <Button key="promptBtn" label="Confirm" variant="brand" onClick={confirm} />
      ]}
      isOpen={isOpen}
      onRequestClose={onRequestClose}
      prompt="error"
      size="medium"
      title="Confirmation Needed to Reveal Section"
    >
      <div className="slds-m-around_medium">
        This section is meant for configuring system settings that only an advanced GRAX admin or
        enablement engineer will be familiar with. Please proceed with caution and reach out to GRAX
        Support if you have any questions.
      </div>
    </Modal>
  );
};

RevealSectionWarning.propTypes = propTypes;

export default RevealSectionWarning;
