import {
  QUICK_START_FORM_INITIAL_LOAD,
  QUICK_START_FORM_SET_PROVIDER,
  QUICK_START_FORM_SET_NAME,
  BASIC_CONFIG_SET_VALUE,
} from "../../consts/configurations/configurationsActions";
import InvalidActionTypeError from "../../models/errors/InvalidActionTypeError";

const quickStartFormReducer = (state, { type, payload }) => {
  switch (type) {
    case QUICK_START_FORM_INITIAL_LOAD:
      return {
        ...state,
        name: payload.name,
        [state.provider]: {
          ...state[state.provider],
          provider: state.provider,
          ...payload,
        },
      };
    case QUICK_START_FORM_SET_PROVIDER:
      return {
        ...state,
        provider: payload.provider,
        name: state.name,

        [payload.provider]: {
          ...state[payload.provider],
          name: state.name,
        },
      };
    case QUICK_START_FORM_SET_NAME:
      return {
        ...state,
        name: payload.name,
        [state.provider]: {
          ...state[state.provider],
          name: payload.name,
        },
      };
    case BASIC_CONFIG_SET_VALUE:
      return {
        ...state,
        [state.provider]: {
          ...state[state.provider],
          name: state.name,
          provider: state.provider,
          ...payload,
        },
      };
    default:
      throw new InvalidActionTypeError();
  }
};

export default quickStartFormReducer;
