import React, { memo, useRef } from "react";
import PropTypes from "prop-types";

import { DEDUP_SCENARIO_MERGE_ACTIONS } from "../../../consts/dedup/componentNames";
import GRAXCombobox from "../../commons/GRAXCombobox";
import { useScenarioStore } from "../../../contexts/scenarioContext";

const propTypes = {
  scenarioActions: PropTypes.array,
  scenarioAction: PropTypes.string,
  onChange: PropTypes.func
};

const MergeActions = ({ scenarioAction, onChange }) => {
  const scenarioStore = useScenarioStore();
  const { scenario_actions } = scenarioStore.metadata.data.data;

  const extractMergeActions = scenarioActions => {
    const options = [];

    for (let index in scenarioActions) {
      let values = scenarioActions[index].values;

      values = values.map(value => ({
        label: value.display_value,
        value: value.code
      }));

      options.push(...values);
    }

    return options;
  };

  const b = useRef(0);
  b.current = b.current + 1;
  //console.log(b);
  return (
    <div className="slds-col slds-size_2-of-5">
      <GRAXCombobox
        options={extractMergeActions(scenario_actions)}
        value={scenarioAction}
        labels={{ placeholder: "Select Option" }}
        required
        onChange={(e, selected) => onChange(selected.length === 1 ? selected[0].value : null)}
      />
    </div>
  );
};

MergeActions.prototype = propTypes;
MergeActions.displayName = DEDUP_SCENARIO_MERGE_ACTIONS;

export default memo(MergeActions);
