import { FETCH_SET_DATA, FETCH_IS_LOADING, FETCH_THROW_ERROR } from "../consts/fetchActions";
import InvalidActionTypeError from "../models/errors/InvalidActionTypeError";

export const initialState = {
  isLoading: null,
  error: null,
  data: null,
  success: null,
  status: null,
};

export const fetchStatusReducer = (state, { type, payload }) => {
  switch (type) {
    case FETCH_IS_LOADING:
      if (payload.isLoading === true) {
        return { ...initialState, isLoading: true };
      }
      return { ...state, isLoading: false };
    case FETCH_SET_DATA:
      return { ...state, data: payload.data, error: null, success: true, isLoading: false };
    case FETCH_THROW_ERROR: {
      const { response = {}, message } = payload.error;
      let error;

      if (response.status >= 500) {
        error = response.statusText || "Internal Server Error";
      } else if (response.data) {
        error = response.data;
      } else if (response.statusText) {
        error = response.statusText;
      } else {
        error = message;
      }

      return {
        ...state,
        data: null,
        success: false,
        isLoading: false,
        error,
      };
    }
    default:
      throw new InvalidActionTypeError();
  }
};
