import React from "react";
import PropTypes from "prop-types";
import { PageHeader, Card } from "@salesforce/design-system-react";

import useConfigurationSummary from "../../../../hooks/configuration/useConfigurationSummary";

const propTypes = {
  configuration: PropTypes.object.isRequired
};

const BasicConfigurationSummary = ({ configuration }) => {
  const { salesforce, elastic, storage } = useConfigurationSummary(configuration);

  return (
    <Card hasNoHeader className="slds-card__body_inner">
      <PageHeader title="Salesforce Source" variant="record-home" details={salesforce} />
      <PageHeader title="Elasticsearch" variant="record-home" details={elastic} />
      <PageHeader title="Cloud Storage" variant="record-home" details={storage} />
    </Card>
  );
};

BasicConfigurationSummary.propTypes = propTypes;

export default BasicConfigurationSummary;
