export const useFetchDuplicates = () => {
  const [state, setConfig] = useFetch(null, { data: { data: { results: [] } } });
  const fetchDuplicates = scenarioId =>
    setConfig({
      url: `${SCENARIO_PROXY_ADDRESS}/${scenarioId}/results`,
      params: { sfdcorgid: "" }
    });
  return [state, fetchDuplicates];
};

export const useGetDuplicates = () => {
  const [state, setConfig] = useFetch();
  const runDeduplication = scenarioId =>
    //FIXME: use proper sfdcorgid
    setConfig({
      url: `${SCENARIO_PROXY_ADDRESS}/${scenarioId}/results?sfdcorgid="`,
      method: "POST"
    });
  return [state, runDeduplication];
};
