import React, { createContext } from "react";

import { useStateContext, useDispatchContext } from "../hooks/configuration/useContextProvider";
import { useConfigurationRetrieve } from "../hooks/configuration/useConfigurationFetch";

const stateContext = createContext();
const dispatchContext = createContext();

export const useConfigurationsContext = () => [
  useStateContext(stateContext),
  useDispatchContext(dispatchContext)
];

export const ConfigurationsProvider = ({ children }) => {
  const [state, dispatch] = useConfigurationRetrieve();

  return (
    <stateContext.Provider value={state}>
      <dispatchContext.Provider value={dispatch}>{children}</dispatchContext.Provider>
    </stateContext.Provider>
  );
};
