import React, { createContext, useContext, useMemo } from "react";

import { DEDUP_PROXY_ADDRESS } from "../consts/addresses";
import useFetch from "../hooks/useFetch";

export const DedupStoreContext = createContext();
export const useScenarioStore = () => useContext(DedupStoreContext);

export const DedupStoreContextProvider = ({ children }) => {
  //TODO: retrieve sfdcorgid from .env
  const [metadata] = useFetch(
    { url: `${DEDUP_PROXY_ADDRESS}/metadata`, params: { sfdcorgid: "" } },
    { data: { data: { operators: [], stopwords: [], synonyms: [] } } }
  );
  // TODO: move salesforce object into a React cache

  const store = useMemo(
    () => ({
      metadata
    }),
    [metadata]
  );

  return <DedupStoreContext.Provider value={store}>{children}</DedupStoreContext.Provider>;
};
