import React from "react";

import {
  ENVIRONMENT_VARIABLE_SET_PUSH_TO_QUEUE,
  ENVIRONMENT_VARIABLE_SET_VARS,
  ENVIRONMENT_VARIABLE_SET_TIME_MACHINE,
  ADVANCED_CONFIG_SET_VALUE,
} from "../../../../../consts/configurations/configurationsActions";
import { useEnvironmentVariablesContext } from "../../../../../contexts/environmentVariablesContext";

import ChangeDataCapture from "./ChangeDataCapture";
import OrgMigrationSettings from "./OrgMigrationSettings";
import PointinTimeRestore from "./PointinTimeRestore";
import PushtoQueue from "./PushtoQueue";
import Other from "./Other";

import {
  useFormDispatch,
  useHandleInput,
  useHandleCheckbox,
} from "../../../../../hooks/useHandleForm";
import {
  ADVANCED_SECTION_CHANGE_DATA_CAPTURE_IS_ENABLE,
  ADVANCED_SECTION_ORG_MIGRATION_SETTINGS_IS_ENABLE,
  ADVANCED_SECTION_POINT_IN_TIME_RESTORE_IS_ENABLE,
  ADVANCED_SECTION_PUSH_TO_QUEUE_IS_ENABLE,
  ADVANCED_SECTION_OTHER_IS_ENABLE,
} from "../../../../../consts/configurations/advancedSections";

const EnvironmentVariables = () => {
  const [state, dispatch] = useEnvironmentVariablesContext();
  const { variables = {}, timeMachine = {}, pushToQueue = {}, asyncIntervalMinutes } = state;
  const { provider, apiEndpoint, apiClientId, apiClientSecret, authorizationToken } = pushToQueue;
  const { queryFile } = timeMachine;
  const {
    cdcEnabled,
    GRAX_SYNCHID,
    RESTORE_SLEEP_DURATION,
    GRAX_OBJECT_TYPES_TO_RESTORE,
    GRAX_MAX_RESTORE,
  } = variables;

  const handleVariablesCheckbox = useHandleCheckbox(ENVIRONMENT_VARIABLE_SET_VARS, dispatch);
  const onBlurVariables = useHandleInput(ENVIRONMENT_VARIABLE_SET_VARS, dispatch);
  const onBlurTimeMachine = useHandleInput(ENVIRONMENT_VARIABLE_SET_TIME_MACHINE, dispatch);
  const onTogglePushToQueue = useFormDispatch(ENVIRONMENT_VARIABLE_SET_PUSH_TO_QUEUE, dispatch);
  const onBlurPushToQueue = useHandleInput(ENVIRONMENT_VARIABLE_SET_PUSH_TO_QUEUE, dispatch);
  const onBlurAsyncIntervalMinutes = useHandleInput(ADVANCED_CONFIG_SET_VALUE, dispatch);

  return (
    <>
      {ADVANCED_SECTION_CHANGE_DATA_CAPTURE_IS_ENABLE === true && (
        <ChangeDataCapture cdcEnabled={cdcEnabled} handleCheckbox={handleVariablesCheckbox} />
      )}
      {ADVANCED_SECTION_ORG_MIGRATION_SETTINGS_IS_ENABLE === true && (
        <OrgMigrationSettings
          GRAX_SYNCHID={GRAX_SYNCHID}
          RESTORE_SLEEP_DURATION={RESTORE_SLEEP_DURATION}
          GRAX_OBJECT_TYPES_TO_RESTORE={GRAX_OBJECT_TYPES_TO_RESTORE}
          GRAX_MAX_RESTORE={GRAX_MAX_RESTORE}
          onBlur={onBlurVariables}
        />
      )}
      {ADVANCED_SECTION_POINT_IN_TIME_RESTORE_IS_ENABLE === true && (
        <PointinTimeRestore queryFile={queryFile} onBlur={onBlurTimeMachine} />
      )}
      {ADVANCED_SECTION_PUSH_TO_QUEUE_IS_ENABLE === true && (
        <PushtoQueue
          provider={provider}
          apiEndpoint={apiEndpoint}
          apiClientId={apiClientId}
          apiClientSecret={apiClientSecret}
          authorizationToken={authorizationToken}
          onToggle={onTogglePushToQueue}
          onBlur={onBlurPushToQueue}
        />
      )}
      {ADVANCED_SECTION_OTHER_IS_ENABLE === true && (
        <Other
          asyncIntervalMinutes={asyncIntervalMinutes}
          asyncIntervalMinutesOnBlur={onBlurAsyncIntervalMinutes}
        />
      )}
    </>
  );
};

export default EnvironmentVariables;
