import {
  SCENARIO_SET_MATCH_CRITERIA,
  SCENARIO_SET_FILTER_CRITERIA,
  SCENARIO_SET_NAME,
  SCENARIO_SET_DESCRIPTION,
  SCENARIO_SET_OBJECT_NAME,
  SCENARIO_SET_IS_ACTIVE,
  SCENARIO_SET_SCENARIO_ACTION,
  SCENARIO_SET_RESULTS_TABLE_FIELDS,
  SCENARIO_SET_FIELDS,
  SCENARIO_LOAD
} from "../../consts/dedup/scenarioActions";

const intialState = {
  isEditMode: false,
  objectIndex: "",
  formState: {
    filterCriteria: [],
    matchCriteria: [{}],
    sfdcorgid: "",
    name: "",
    description: "",
    object_name: "",
    is_active: true,
    scenario_action: "MANUAL_MERGE",
    fields: [],
    results_table_fields: [],
    records_affected: null,
    groups_count: null
  },
  editMode: {}
};

const scenarioReducer = (state, action) => {
  switch (action.type) {
    case SCENARIO_LOAD: {
      const { payload } = action;
      return {
        ...state,
        isEditMode: action.payload.isEditMode,
        formState: {
          matchCriteria: payload.matchCriteria,
          filterCriteria: action.payload.filterCriteria,
          name: payload.name,
          description: payload.description,
          object_name: payload.object_name,
          results_table_fields: payload.results_table_fields,
          fields: payload.fields,
          is_active: payload.is_active,
          scenario_action: payload.scenario_action
        }
      };
    }
    case SCENARIO_SET_MATCH_CRITERIA:
    case SCENARIO_SET_FILTER_CRITERIA:
    case SCENARIO_SET_NAME:
    case SCENARIO_SET_DESCRIPTION:
    case SCENARIO_SET_IS_ACTIVE:
    case SCENARIO_SET_SCENARIO_ACTION: {
      const { payload } = action;
      let newState = { ...state, formState: { ...state.formState, ...payload } };

      if (state.isEditMode === true) {
        newState = {
          ...newState,
          editMode: {
            ...state.editMode,
            ...payload
          }
        };
      }

      return newState;
    }
    case SCENARIO_SET_RESULTS_TABLE_FIELDS: {
      const { payload } = action;
      const resultsTableFields = payload.fields.map(field => field.value);

      let newState = {
        ...state,
        formState: { ...state.formState, results_table_fields: resultsTableFields }
      };

      if (state.isEditMode === true) {
        newState = {
          ...newState,
          editMode: {
            ...newState.editMode,
            results_table_fields: resultsTableFields
          }
        };
      }

      return newState;
    }
    case SCENARIO_SET_FIELDS: {
      const { payload } = action;
      const fields = payload.fields.map(field => field.value);

      let newState = {
        ...state,
        formState: { ...state.formState, fields }
      };

      if (state.isEditMode === true) {
        newState = {
          ...newState,
          editMode: {
            ...newState.editMode,
            fields
          }
        };
      }

      return newState;
    }
    case SCENARIO_SET_OBJECT_NAME: {
      const { payload } = action;
      const matchCriteria = state.formState.matchCriteria.map(matchCriteria => ({
        ...matchCriteria,
        field_name: "",
        operator: ""
      }));
      const filterCriteria = state.formState.filterCriteria.map(filterCriteria => ({
        ...filterCriteria,
        field_name: "",
        operator: ""
      }));
      //TODO: move objectIndex from state
      let newState = {
        ...state,
        objectIndex: payload.objectIndex,
        formState: {
          ...state.formState,
          object_name: payload.object_name,
          matchCriteria,
          filterCriteria
        }
      };
      if (state.isEditMode === true) {
        newState = {
          ...newState,
          editMode: {
            ...newState.editMode,
            object_name: payload.object_name,
            matchCriteria,
            filterCriteria
          }
        };
      }
      return newState;
    }
    default:
      throw new Error();
  }
};

export { scenarioReducer, intialState };
