/* eslint-disable react/require-default-props */
import React, { useEffect } from "react";
import PropTypes from "prop-types";

import { Button } from "@salesforce/design-system-react";

import { BASIC_CONFIG_SET_VALUE } from "../../../../consts/configurations/configurationsActions";
import {
  SALESFORCE_OAUTH_URL,
  SALESFORCE_DATASOURCES_SOURCE,
  SALESFORCE_DATASOURCES_EINSTEIN,
  SALESFORCE_DATASOURCES_BULKRESTORE,
  SALESFORCE_OAUTH_TOKENURL,
  SALESFORCE_OAUTH_ERRORACCESDENIED,
} from "./consts";

import { isOAuthCredential, toggleCrosswindowMessageListener } from "./utils";

import StatefulInput from "../../../commons/StatefulInput";
import URLStateFulInput from "./URLStateFulInput";
import OAuthFlowWindow from "./OAuthFlowWindow";

import { useToggleSpinner } from "../../../../hooks/useInteractiveFetchStatus";
import { useFormDispatch } from "../../../../hooks/useHandleForm";
import useFetch from "../../../../hooks/useFetch";
import { useSFDCHandleInput } from "./hooks";

const propTypes = {
  dataSource: PropTypes.objectOf({
    userName: PropTypes.strings,
    url: PropTypes.string,
    graxOAuthURL: PropTypes.string,
    organizationId: PropTypes.string,
  }),
  dispatch: PropTypes.func.isRequired,
  onValidated: PropTypes.func,
  variant: PropTypes.oneOf([
    SALESFORCE_DATASOURCES_SOURCE,
    SALESFORCE_DATASOURCES_EINSTEIN,
    SALESFORCE_DATASOURCES_BULKRESTORE,
  ]).isRequired,
};

const setTokenUrl = (organizationId) => ({
  url: `${SALESFORCE_OAUTH_TOKENURL}?sfdcorgid=${organizationId}`,
});

let urlRef = null;

const OAuth = ({ dataSource, variant, dispatch, onValidated }) => {
  const handleOnChange = useFormDispatch(BASIC_CONFIG_SET_VALUE, dispatch);
  const onBlur = useSFDCHandleInput(variant, dispatch);
  const [{ isLoading, error, data, success }, refreshToken] = useFetch();
  useToggleSpinner(isLoading);

  const showScopeNotification = (customMessage, status = false) => {
    if (status === true) {
      onValidated({
        status: "successful",
        customMessage,
      });
    } else {
      onValidated({
        status: "failed",
        customMessage: `Invalid OAuth credentials: ${customMessage}`,
      });
    }
  };

  useEffect(() => {
    if (success === true && data && data.access_token) {
      showScopeNotification("OAuth credentials validated", true);
    } else if (success === false || error) {
      showScopeNotification(error.error_description || "error description is missing");
    }
  }, [data, error, success]);

  useEffect(() => {
    toggleCrosswindowMessageListener((event) => {
      if (event.data && event.data.error) {
        if (event.data.error === SALESFORCE_OAUTH_ERRORACCESDENIED) return;

        showScopeNotification(event.data.error_description || "error description is missing");
      } else if (event.data && event.data.status === "success") {
        const oauthPopupOrigin = new URL(SALESFORCE_OAUTH_URL).origin;
        if (event.origin !== oauthPopupOrigin) {
          showScopeNotification(`Cross Origin authentication is not supported: ${event.origin}`);
          // return;
        }
        showScopeNotification("Successful OAuth authentication", true);
        handleOnChange("graxOAuthURL", SALESFORCE_OAUTH_TOKENURL, { variant });
        handleOnChange("organizationId", event.data.org_id, { variant });
        handleOnChange("userName", event.data.user_name, { variant });
      }
    });

    return () => toggleCrosswindowMessageListener();
  }, []);

  return (
    <>
      {isOAuthCredential(dataSource) === true ? (
        <>
          <div className="slds-grid slds-gutters_small">
            <div className="slds-col slds-size_1-of-3">
              <URLStateFulInput
                url={dataSource.url}
                onBlur={onBlur}
                inputRef={(e) => {
                  urlRef = e;
                }}
              />
            </div>
            <div className="slds-col slds-size_1-of-3">
              <StatefulInput
                readOnly
                label="SFDC Username"
                value={dataSource.userName}
                id="userName"
                onBlur={() => {}}
              />
            </div>
            <div className="slds-col slds-size_1-of-3">
              <StatefulInput
                readOnly
                label="SFDC Organization ID (18 Digit)"
                value={dataSource.organizationId}
                id="organizationId"
                onBlur={() => {}}
              />
            </div>
          </div>
          <div className="slds-grid slds-gutters_small">
            <div className="slds-col slds-size_1-of-3" />
          </div>
          <div className="slds-grid slds-gutters_small slds-m-top_small">
            <div className="slds-col slds-size_1-of-3">
              <Button
                label="Validate OAuth authentication"
                onClick={() => refreshToken(setTokenUrl(dataSource.organizationId))}
              />
              <OAuthFlowWindow
                label="Change authentication"
                loginurl={dataSource.url}
                urlRef={urlRef}
              />
            </div>
          </div>
        </>
      ) : (
        <>
          <div className="slds-grid slds-gutters_small">
            <div className="slds-col slds-size_1-of-3">
              <URLStateFulInput
                url={dataSource.url}
                onBlur={onBlur}
                inputRef={(e) => {
                  urlRef = e;
                }}
              />
            </div>
          </div>
          <div className="slds-grid slds-gutters_small slds-m-top_small">
            <div className="slds-col slds-size_1-of-3">
              <OAuthFlowWindow
                label="Authenticate using OAuth"
                loginurl={dataSource.url}
                urlRef={urlRef}
              />
            </div>
          </div>
        </>
      )}
    </>
  );
};

OAuth.propTypes = propTypes;

export default OAuth;
