import { useState, useEffect } from "react";
import VENDOR_DISPLAY_NAME_MAPPING from "../../consts/configurations/vendorDisplayNameMapping";

const iconSettingsTypes = {
  successful: {
    label: "success",
    category: "utility",
    colorVariant: "default",
    name: "success",
  },
  failed: {
    label: "failure",
    category: "utility",
    colorVariant: "error",
    name: "error",
  },
};

const useVendorValidityMessage = (variant, status, customMessage) => {
  const [_status, setStatus] = useState(status);
  useEffect(() => setStatus(status), [status]);

  if (_status !== "") {
    const displayProvider = VENDOR_DISPLAY_NAME_MAPPING[variant];
    let message;

    const iconSettings =
      iconSettingsTypes[_status === "invalidOrganizationId" ? "failed" : _status];

    if (customMessage) {
      message = customMessage;
    } else {
      message = {
        successful: `${displayProvider} credentials validated`,
        failed: `Invalid ${displayProvider} credentials`,
        invalidOrganizationId:
          "You cannot enter SFDC credentials for a SFDC org different than the org you are currently in",
      }[_status];
    }

    return { iconSettings, message };
  }

  return {};
};

export default useVendorValidityMessage;
